import { Modal } from "bootstrap";
import dayjs from "dayjs";
import "dayjs/locale/de";
import arraySupport from "dayjs/plugin/arraySupport";
import customParseFormat from "dayjs/plugin/customParseFormat";
import duration from "dayjs/plugin/duration";
import isoWeek from "dayjs/plugin/isoWeek";
import isSameOrAfter from "dayjs/plugin/isSameOrAfter";
import isSameOrBefore from "dayjs/plugin/isSameOrBefore";
import LocalizedFormat from "dayjs/plugin/LocalizedFormat";
import minMax from "dayjs/plugin/minMax";
import { isHoliday } from "feiertagejs";
import {
	buttonDisable,
	clearLoading,
	createEditorModalFooter,
	createModal,
	createModalBodyCheckbox,
	createModalBodyFillerElement,
	createModalBodyInputElement,
	createModalBodySelectElement,
	createModalBodyShowElement,
	createModalBodyTitelElement,
	createShowModalFooter,
	DatenSortieren,
	download,
	FetchRetry,
	isObject,
	saveDaten,
	saveTableData,
	setLoading,
	tableToArray,
} from "./Core.js";
import { createCustomTable, CustomTable, Row } from "./CustomTable";
import { createSnackBar } from "./js-snackbar.js";
dayjs.locale("de");
dayjs.extend(arraySupport);
dayjs.extend(customParseFormat);
dayjs.extend(LocalizedFormat);
dayjs.extend(isoWeek);
dayjs.extend(duration);
dayjs.extend(isSameOrBefore);
dayjs.extend(isSameOrAfter);
dayjs.extend(minMax);

window.addEventListener("load", () => {
	const datetimeParser = value => dayjs(value).format("DD.MM.YYYY, LT"),
		timeZeroParser = value => (!value ? "" : value),
		ftBZ = createCustomTable("tableBZ", {
			columns: [
				{ name: "beginB", title: "Von", parser: datetimeParser, sortable: true, sorted: true, direction: "ASC" },
				{ name: "endeB", title: "Bis", parser: datetimeParser, sortable: true },
				{ name: "pauseB", title: "Pause", parser: timeZeroParser, breakpoints: "xs" },
			],
			rows: DataBZ(),
			sorting: { enabled: true },
			editing: {
				enabled: true,
				addRow: () => {
					const $modal = createEditorModalBereitschaftsZeit(ftBZ, "Zeitraum hinzufügen");
					$modal.row = ftBZ;
					new Modal($modal).show();
				},
				editRow: row => {
					const $modal = createEditorModalBereitschaftsZeit(row, "Zeitraum bearbeiten");
					$modal.row = row;
					new Modal($modal).show();
				},
				showRow: row => {
					const $modal = createShowModalBereitschaft(row, "Zeitraum anzeigen");
					$modal.row = row;
					new Modal($modal).show();
				},
				deleteRow: row => {
					row.deleteRow();
					saveTableData("tableBZ", ftBZ);
				},
				deleteAllRows: () => {
					if (confirm("Möchtest du wirklich alle Zeilen löschen?")) {
						ftBZ.rows.load([]);
						buttonDisable(false);
						localStorage.setItem("dataBZ", JSON.stringify([]));
					}
				},
			},
		});

	// ----------------------------- Bereitschaftseinsätze ------------------------------------------------

	const ftBE = createCustomTable("tableBE", {
		columns: [
			{ name: "tagBE", title: "Tag", sortable: true, sorted: true, direction: "ASC" },
			{ name: "auftragsnummerBE", title: "Auftrags-Nr.", classes: ["custom-text-truncate"] },
			{ name: "beginBE", title: "Von", breakpoints: "sm", type: "time" },
			{ name: "endeBE", title: "Bis", breakpoints: "sm", type: "time" },
			{ name: "lreBE", title: "LRE", breakpoints: "" },
			{ name: "privatkmBE", title: "Privat Km", parser: timeZeroParser, breakpoints: "md" },
		],
		rows: DataBE(),
		sorting: { enabled: true },
		editing: {
			enabled: true,
			addRow: () => {
				const $modal = createEditorModalBereitschaftsEinsatz(ftBE, "Einsatz hinzufügen");
				$modal.row = ftBE;
				new Modal($modal).show();
			},
			editRow: row => {
				const $modal = createEditorModalBereitschaftsEinsatz(row, "Einsatz bearbeiten");
				$modal.row = row;
				new Modal($modal).show();
			},
			showRow: row => {
				const $modal = createShowModalBereitschaft(row, "Einsatz anzeigen");
				$modal.row = row;
				new Modal($modal).show();
			},
			deleteRow: row => {
				row.deleteRow();
				saveTableData("tableBE", ftBE);
			},
			deleteAllRows: () => {
				if (confirm("Möchtest du wirklich alle Zeilen löschen?")) {
					ftBE.rows.load([]);
					buttonDisable(false);
					localStorage.setItem("dataBE", JSON.stringify([]));
				}
			},
		},
	});

	// "click"-Eventlistener

	document.querySelector("#btnESZ").addEventListener("click", createAddModalBereitschaftsZeit);

	document.querySelector("#btnESE").addEventListener("click", createAddModalBereitschaftsEinsatz);

	document.getElementById("btnSaveB").addEventListener("click", () => {
		saveDaten(document.getElementById("btnSaveB"));
	});
	document.getElementById("btnDownloadB").addEventListener("click", () => {
		download(document.getElementById("btnDownloadB"), "B");
	});
});

// create Modal

function createAddModalBereitschaftsZeit() {
	const { modal, form } = createModal(
		"Neue Bereitschaft eingeben",
		true,
		null,
		createBodyElement,
		createEditorModalFooter(),
		SubmitEventListener
	);
	new Modal(modal).show();

	function createBodyElement() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		const vorgabenB = JSON.parse(localStorage.getItem("VorgabenB"));
		const Monat = +localStorage.getItem("Monat");
		const Jahr = +localStorage.getItem("Jahr");
		let datum = dayjs([Jahr, Monat - 1, dayjs().date()]).isoWeekday(3);
		if (datum.isSameOrBefore(dayjs([Jahr, Monat - 1]).startOf("M"))) {
			datum = datum.add(1, "w");
		} else if (datum.isSameOrAfter(dayjs([Jahr, Monat - 1]).endOf("M"))) {
			datum = datum.subtract(1, "w");
		}

		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch bereitschaft",
				id: "b12",
				text: "B1 - B2?",
				status: false,
				eventListener: () => b12aendern(JSON.parse(localStorage.getItem("VorgabenB"))),
			})
		);
		modalBody.appendChild(createModalBodyTitelElement("Bereitschafts Anfang"));
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-7 col-sm-6 pb-3",
				title: "Datum",
				name: "bA",
				value: datum.format("YYYY-MM-DD"),
				type: "Date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
				eventListener: () => datumAnpassen(JSON.parse(localStorage.getItem("VorgabenB"))),
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-5 col-sm-6 pb-3",
				title: "Von",
				name: "bAT",
				value: vorgabenB[1],
				type: "time",
				required: true,
				disabled: true,
			})
		);
		modalBody.appendChild(createModalBodyTitelElement("Bereitschafts Ende"));
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-7 col-sm-6 pb-3",
				title: "Datum",
				name: "bE",
				value: datum.isoWeekday(10).format("YYYY-MM-DD"),
				type: "Date",
				required: true,
				disabled: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.add(1, "M").endOf("M").format("YYYY-MM-DD"),
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-5 col-sm-6 pb-3",
				title: "Bis",
				name: "bET",
				value: vorgabenB[0],
				type: "time",
				required: true,
				disabled: true,
			})
		);
		modalBody.appendChild(document.createElement("hr"));
		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch bereitschaft",
				id: "nacht",
				text: "Nachtschicht",
				status: true,
				eventListener: nachtAusblenden,
			})
		);

		const nachtWrapper = document.createElement("div");
		nachtWrapper.id = "nachtschicht";
		nachtWrapper.className = "row m-0 p-0";
		modalBody.appendChild(nachtWrapper);

		nachtWrapper.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch bereitschaft",
				id: "sa",
				text: "Nachtschicht ab Samstag?",
				status: false,
				eventListener: () => saAEndern(JSON.parse(localStorage.getItem("VorgabenB"))),
			})
		);
		nachtWrapper.appendChild(createModalBodyTitelElement("Nacht Anfang"));
		nachtWrapper.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-7 col-sm-6 pb-3",
				title: "Datum",
				name: "nA",
				value: datum.isoWeekday(7).format("YYYY-MM-DD"),
				type: "Date",
				required: true,
				disabled: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.add(1, "M").endOf("M").format("YYYY-MM-DD"),
			})
		);
		nachtWrapper.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-5 col-sm-6 pb-3",
				title: "Von",
				name: "nAT",
				value: vorgabenB[4],
				type: "time",
				required: true,
				disabled: true,
			})
		);
		nachtWrapper.appendChild(createModalBodyTitelElement("Nacht Ende"));
		nachtWrapper.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-7 col-sm-6 pb-3",
				title: "Datum",
				name: "nE",
				value: datum.isoWeekday(10).format("YYYY-MM-DD"),
				type: "Date",
				required: true,
				disabled: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.add(1, "M").endOf("M").format("YYYY-MM-DD"),
			})
		);
		nachtWrapper.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-5 col-sm-6 pb-3",
				title: "Bis",
				name: "nET",
				value: vorgabenB[5],
				type: "time",
				required: true,
				disabled: true,
			})
		);
		const customHrElement = document.createElement("hr");
		customHrElement.classList.add("mt-3");

		modalBody.appendChild(customHrElement);
		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch bereitschaft",
				id: "eigen",
				text: "Eingabe Eigene Werte?",
				status: false,
				eventListener: () => eigeneWerte(JSON.parse(localStorage.getItem("VorgabenB"))),
			})
		);

		return modalBody;
	}

	function SubmitEventListener() {
		return e => {
			if (form.checkValidity && !form.checkValidity()) return;
			e.preventDefault();

			bereitschaftEingabeWeb(localStorage.getItem("accessToken"));

			Modal.getInstance(modal).hide();
			saveTableData("tableBZ", document.querySelector("#tableBZ").CustomTable);
		};
	}
}
function createEditorModalBereitschaftsZeit(row, title) {
	const { modal, form } = createModal(
		title,
		true,
		"sm",
		createBodyElement,
		createEditorModalFooter(null, row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);

	return modal;

	function createBodyElement() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		if (row instanceof Row) {
			row.columns.array.forEach(column => {
				let datum, min, max;
				switch (column.name) {
					case "editing":
						break;
					case "beginB":
					case "endeB":
						datum = dayjs(row.cells[column.name]);
						min = datum.startOf("M").format("YYYY-MM-DDTHH:mm");
						max = datum.add(1, "M").startOf("M").format("YYYY-MM-DDTHH:mm");
						modalBody.appendChild(
							createModalBodyInputElement({
								divClass: "form-floating col-12 pb-3",
								title: column.title,
								name: column.name,
								value: dayjs(row.cells[column.name]).format("YYYY-MM-DDTHH:mm"),
								type: "datetime-local",
								required: true,
								min,
								max,
							})
						);
						break;
					default:
						modalBody.appendChild(
							createModalBodyInputElement({
								divClass: "form-floating col-12",
								title: column.title,
								name: column.name,
								value: column.parser(row.cells[column.name]),
								type: "number",
								min: 0,
								max: 60,
							})
						);
						break;
				}
			});
		} else if (row instanceof CustomTable) {
			const Monat = +localStorage.getItem("Monat");
			const Jahr = +localStorage.getItem("Jahr");
			row.columns.array.forEach(column => {
				let datum, min, max;
				switch (column.name) {
					case "editing":
						break;
					case "beginB":
					case "endeB":
						datum = dayjs([Jahr, Monat - 1, dayjs().date()]);
						min = datum.startOf("M").format("YYYY-MM-DDTHH:mm");
						max = datum.add(1, "M").startOf("M").format("YYYY-MM-DDTHH:mm");
						modalBody.appendChild(
							createModalBodyInputElement({
								divClass: "form-floating col-12 pb-3",
								title: column.title,
								name: column.name,
								value: null,
								type: "datetime-local",
								required: true,
								min,
								max,
							})
						);
						break;
					default:
						modalBody.appendChild(
							createModalBodyInputElement({
								divClass: "form-floating col-12",
								title: column.title,
								name: column.name,
								value: 0,
								type: "number",
								min: 0,
								max: 60,
							})
						);
						break;
				}
			});
		}

		return modalBody;
	}

	function SubmitEventListener() {
		return e => {
			if (form.checkValidity && !form.checkValidity()) return;
			e.preventDefault();

			const row = modal.row,
				values = {
					beginB: dayjs(form.querySelector("#beginB").value).toISOString(),
					endeB: dayjs(form.querySelector("#endeB").value).toISOString(),
					pauseB: Number(form.querySelector("#pauseB").value),
				};

			row instanceof Row ? row.val(values) : row.rows.add(values);

			Modal.getInstance(modal).hide();
			saveTableData("tableBZ", row.CustomTable || row);
		};
	}
}

function createAddModalBereitschaftsEinsatz() {
	const { modal, form } = createModal(
		"Neuen Bereitschaftseinsatz eingeben",
		true,
		null,
		createBodyElement,
		createEditorModalFooter(),
		SubmitEventListener
	);
	new Modal(modal).show();

	function createBodyElement() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		const datum = dayjs([+localStorage.getItem("Jahr"), +localStorage.getItem("Monat") - 1, dayjs().date()]);

		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "Datum",
				name: "Datum",
				value: datum.format("YYYY-MM-DD"),
				type: "date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 pb-3",
				title: "SAP-Nr / Einsatzbeschreibung",
				name: "SAPNR",
				value: "",
				type: "text",
				required: true,
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "Von",
				name: "ZeitVon",
				value: null,
				type: "time",
				required: true,
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "Bis",
				name: "ZeitBis",
				value: null,
				type: "time",
				required: true,
			})
		);
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "LRE",
				name: "LRE",
				value: null,
				options: [
					{ value: "", text: "Bitte Einsatz auswählen", disabled: true, selected: true },
					{ value: "LRE 1", text: "LRE 1" },
					{ value: "LRE 2", text: "LRE 2" },
					{ value: "LRE 1/2 ohne x", text: "LRE 1/2 ohne x" },
					{ value: "LRE 3", text: "LRE 3" },
					{ value: "LRE 3 ohne x", text: "LRE 3 ohne x" },
				],
				required: true,
			})
		);
		modalBody.appendChild(createModalBodyFillerElement());
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "Privat Km",
				name: "privatkm",
				value: 0,
				type: "number",
				min: 0,
			})
		);

		const wrapper = document.createElement("div");
		wrapper.className = "col-12";

		wrapper.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch bereitschaft",
				id: "berZeit",
				text: "zusätzliche Bereitschaftszeit Eingeben?<br><small>(z.B. LRE3 Außerhalb der Bereitschaft.)</small>",
				status: false,
			})
		);
		modalBody.appendChild(wrapper);

		return modalBody;
	}

	function SubmitEventListener() {
		return e => {
			if (form.checkValidity && !form.checkValidity()) return;
			e.preventDefault();

			sendDataE();

			Modal.getInstance(modal).hide();
			saveTableData("tableBE", document.querySelector("#tableBE").CustomTable);
		};
	}
}
function createEditorModalBereitschaftsEinsatz(row, titel) {
	const { modal, form } = createModal(
		titel,
		true,
		null,
		createBodyElement,
		createEditorModalFooter(null, row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);

	return modal;

	function createBodyElement() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		let datum;
		if (row instanceof Row) {
			datum = dayjs(row.cells.tagBE, "DD.MM.YYYY");
		} else if (row instanceof CustomTable) {
			const Monat = +localStorage.getItem("Monat");
			const Jahr = +localStorage.getItem("Jahr");
			datum = dayjs([Jahr, Monat - 1, dayjs().date()]);
		} else throw Error("unbekannter Fehler");

		row.columns.array.forEach(column => {
			switch (column.name) {
				case "tagBE":
					modalBody.appendChild(
						createModalBodyInputElement({
							divClass: "form-floating col-12 col-sm-6 pb-3",
							title: column.title,
							name: column.name,
							value: datum.format("YYYY-MM-DD"),
							type: "date",
							required: true,
							min: datum.startOf("M").format("YYYY-MM-DD"),
							max: datum.endOf("M").format("YYYY-MM-DD"),
						})
					);
					break;

				case "auftragsnummerBE":
					modalBody.appendChild(
						createModalBodyInputElement({
							divClass: "form-floating col-12 pb-3",
							title: "SAP-Nr / Einsatzbeschreibung",
							name: column.name,
							value: (row.cells && row.cells[column.name]) || "",
							type: "text",
							required: true,
						})
					);
					break;

				case "beginBE":
				case "endeBE":
					modalBody.appendChild(
						createModalBodyInputElement({
							divClass: "form-floating col-12 col-sm-6 pb-3",
							title: column.title,
							name: column.name,
							value: (row.cells && row.cells[column.name]) || "",
							type: "time",
							required: true,
						})
					);
					break;

				case "lreBE":
					modalBody.appendChild(
						createModalBodySelectElement({
							divClass: "form-floating col-12 col-sm-6 pb-3",
							title: column.title,
							name: column.name,
							value: (row.cells && row.cells[column.name]) || "",
							options: [
								{ value: "", text: "Bitte Einsatz auswählen", disabled: true, selected: true },
								{ value: "LRE 1", text: "LRE 1" },
								{ value: "LRE 2", text: "LRE 2" },
								{ value: "LRE 1/2 ohne x", text: "LRE 1/2 ohne x" },
								{ value: "LRE 3", text: "LRE 3" },
								{ value: "LRE 3 ohne x", text: "LRE 3 ohne x" },
							],
							required: true,
						})
					);
					modalBody.appendChild(createModalBodyFillerElement());
					break;

				case "privatkmBE":
					modalBody.appendChild(
						createModalBodyInputElement({
							divClass: "form-floating col-12 col-sm-6",
							title: column.title,
							name: column.name,
							value: (row.cells && row.cells[column.name]) || 0,
							type: "number",
							min: 0,
						})
					);
					break;

				default:
					break;
			}
		});

		return modalBody;
	}

	function SubmitEventListener() {
		return e => {
			if (form.checkValidity && !form.checkValidity()) return;
			e.preventDefault();

			const row = modal.row,
				values = {
					tagBE: dayjs(form.querySelector("#tagBE").value).format("DD.MM.YYYY"),
					auftragsnummerBE: form.querySelector("#auftragsnummerBE").value,
					beginBE: form.querySelector("#beginBE").value,
					endeBE: form.querySelector("#endeBE").value,
					lreBE: form.querySelector("#lreBE").value,
					privatkmBE: Number(form.querySelector("#privatkmBE").value),
				};

			row instanceof Row ? row.val(values) : row.rows.add(values);

			Modal.getInstance(modal).hide();
			saveTableData("tableBE", row.CustomTable || row);
		};
	}
}

function createShowModalBereitschaft(row, title) {
	const { modal } = createModal(title, false, "sm", createModalBody, createShowModalFooter);
	return modal;

	function createModalBody() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body";

		row.columns.array.forEach(column => {
			if (column.editing) return;
			modalBody.appendChild(
				createModalBodyShowElement({
					divClass: "",
					labelClass: "col-5 col-form-label text-wrap fw-bold",
					spanClass: "col-7 align-middle text-break my-auto",
					title: `${column.title}:`,
					name: column.name,
					text: column.parser(row.cells[column.name]),
				})
			);
		});

		return modalBody;
	}
}

// convert Daten

export function DataBZ(data) {
	if (data == null) {
		if (localStorage.getItem("dataBZ") == null) return [];
		data = JSON.parse(localStorage.getItem("dataBZ"));
	}
	if (data.length === 0 || isObject(data[0])) return data;

	const data1 = [];
	data.forEach((row, i) => {
		data1[i] = {
			beginB: row[0],
			endeB: row[1],
			pauseB: row[2] === "" ? 0 : Number(row[2]),
		};
	});
	return data1;
}

export function DataBE(data) {
	if (data == null) {
		if (localStorage.getItem("dataBE") == null) return [];
		data = JSON.parse(localStorage.getItem("dataBE"));
	}
	if (data.length === 0 || isObject(data[0])) return data;

	const data1 = [];
	data.forEach((row, i) => {
		data1[i] = {
			tagBE: row[0],
			auftragsnummerBE: row[1],
			beginBE: row[2],
			endeBE: row[3],
			lreBE: row[4],
			privatkmBE: row[5] === "" ? 0 : Number(row[5]),
		};
	});
	return data1;
}

// Eingabemaske Bereitschaftszeiten

function b12aendern(
	vorgabenB = JSON.parse(localStorage.getItem("VorgabenB")),
	datum = dayjs(document.getElementById("bA").value)
) {
	if (document.getElementById("b12").checked) {
		document.getElementById("bE").value = datum.isoWeekday(6).format("YYYY-MM-DD");
		document.getElementById("bET").value = vorgabenB[3];
		document.getElementById("nacht").checked = false;
	} else {
		document.getElementById("bE").value = datum.isoWeekday(10).format("YYYY-MM-DD");
		document.getElementById("bET").value = vorgabenB[0];
		document.getElementById("nacht").checked = true;
	}
	nachtAusblenden();
}

function nachtAusblenden() {
	if (document.getElementById("nacht").checked) {
		document.getElementById("nachtschicht").style.display = "flex";
	} else {
		document.getElementById("nachtschicht").style.display = "none";
	}
}

function saAEndern(
	vorgabenB = JSON.parse(localStorage.getItem("VorgabenB")),
	datum = dayjs(document.getElementById("bA").value)
) {
	if (document.getElementById("sa").checked) {
		document.getElementById("nA").value = datum.isoWeekday(6).format("YYYY-MM-DD");
		document.getElementById("nAT").value = vorgabenB[3];
		document.getElementById("nE").value = datum.isoWeekday(10).format("YYYY-MM-DD");
	} else {
		document.getElementById("nA").value = datum.isoWeekday(7).format("YYYY-MM-DD");
		document.getElementById("nAT").value = vorgabenB[4];
		document.getElementById("nE").value = datum.isoWeekday(10).format("YYYY-MM-DD");
	}
}

function datumAnpassen(vorgabenB = JSON.parse(localStorage.getItem("VorgabenB"))) {
	const datum = dayjs(document.getElementById("bA").value);
	b12aendern(vorgabenB, datum);
	saAEndern(vorgabenB, datum);
}

function eigeneWerte(vorgabenB = JSON.parse(localStorage.getItem("VorgabenB"))) {
	if (document.getElementById("eigen").checked) {
		document.getElementById("bAT").disabled = false;
		document.getElementById("bE").disabled = false;
		document.getElementById("bET").disabled = false;
		document.getElementById("nA").disabled = false;
		document.getElementById("nAT").disabled = false;
		document.getElementById("nE").disabled = false;
		document.getElementById("nET").disabled = false;
	} else {
		document.getElementById("bAT").disabled = true;
		document.getElementById("bE").disabled = true;
		document.getElementById("bET").disabled = true;
		document.getElementById("nA").disabled = true;
		document.getElementById("nAT").disabled = true;
		document.getElementById("nE").disabled = true;
		document.getElementById("nET").disabled = true;
		datumAnpassen(vorgabenB);
		document.getElementById("bAT").value = vorgabenB[1];
		b12aendern(vorgabenB);
		document.getElementById("nET").value = vorgabenB[5];
	}
}

// Eingabemaske Bereitschafteinsatz

function sendDataE() {
	setLoading("btnESE");

	const daten = {
		tagBE: dayjs(document.getElementById("Datum").value).format("DD.MM.YYYY"),
		auftragsnummerBE: document.getElementById("SAPNR").value,
		beginBE: document.getElementById("ZeitVon").value,
		endeBE: document.getElementById("ZeitBis").value,
		lreBE: document.querySelector("#LRE").value,
		privatkmBE: Number(document.getElementById("privatkm").value),
	};

	const berZeit = document.getElementById("berZeit").checked;

	console.log(daten);
	const ftBE = document.getElementById("tableBE").instance;
	ftBE.rows.add(daten);
	saveTableData("tableBE", ftBE);

	document.getElementById("SAPNR").value = "";
	document.getElementById("ZeitVon").value = "";
	document.getElementById("ZeitBis").value = "";
	document.getElementById("privatkm").value = "";
	document.getElementById("berZeit").checked = false;
	document.getElementById("LRE").value = "";

	if (berZeit) {
		daten.tagBE = document.getElementById("Datum").value;
		const bereitschaftsAnfang = dayjs(`${daten.tagBE}T${daten.beginBE}`),
			bereitschaftsEnde = dayjs(`${daten.tagBE}T${daten.endeBE}`).isBefore(bereitschaftsAnfang)
				? dayjs(`${daten.tagBE}T${daten.endeBE}`).add(1, "d")
				: dayjs(`${daten.tagBE}T${daten.endeBE}`),
			dataTable = tableToArray("tableBZ");

		const data = BereitschaftEingabe(
			bereitschaftsAnfang,
			bereitschaftsEnde,
			bereitschaftsEnde,
			bereitschaftsEnde,
			false,
			dataTable
		);

		if (data == null) {
			clearLoading("btnESE");
			createSnackBar({
				message: "Bereitschaft<br/>Zeitraum bereits vorhanden!",
				status: "warning",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}

		localStorage.setItem("dataBZ", JSON.stringify(data));
		document.getElementById("tableBZ").instance.rows.load(DataBZ(data));
		createSnackBar({
			message:
				"Bereitschaft<br/>Neuer Zeitraum hinzugefügt</br>Speichern nicht vergessen!</br></br>Berechnung wird erst nach Speichern aktualisiert.",
			status: "success",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
	}

	clearLoading("btnESE");
}

