window.addEventListener("load", () => {
	if (!storageAvailable("localStorage")) {
		createSnackBar({
			message: "Bitte Cookies zulassen!",
			dismissible: true,
			status: "error",
			timeout: false,
			position: "tc",
			fixed: false,
		});
	}
	let offlineSnackbar;
	if (!navigator.onLine) {
		setOffline();
	}
	window.addEventListener("offline", () => {
		setOffline();
	});
	function setOffline() {
		setDisableButton(true);
		offlineSnackbar = createSnackBar({
			message: "Du bist offline",
			dismissible: false,
			status: "error",
			timeout: false,
			position: "tr",
			fixed: true,
		});
		window.addEventListener(
			"online",
			() => {
				setDisableButton(false);
				changeMonatJahr();
				offlineSnackbar.Close();
				createSnackBar({
					message: "Du bist wieder online",
					dismissible: false,
					status: "info",
					timeout: 2000,
					position: "tr",
					fixed: true,
				});
			},
			{ once: true }
		);
	}

	if (localStorage.getItem("accessToken")) {
		let url = document.location.toString();
		if (url.match("#")) {
			let tab = url.split("#")[1];
			console.log(tab);
			let sel = document.querySelector("#" + tab + "-tab");
			console.log(sel);
			Tab.getOrCreateInstance(sel).show();
			window.scrollTo(0, 1);
		}
	}

	const popoverTriggerList = document.querySelectorAll('[data-bs-toggle="popover"]');
	[...popoverTriggerList].map(popoverTriggerEl => new Popover(popoverTriggerEl));
});

