const storageAvailable = type => {
	let storage;
	try {
		storage = window[type];
		let x = "__storage_test__";
		storage.setItem(x, x);
		storage.removeItem(x);
		return true;
	} catch (e) {
		return (
			e instanceof DOMException &&
			// everything except Firefox
			(e.code === 22 ||
				// Firefox
				e.code === 1014 ||
				// test name field too, because code might not be present
				// everything except Firefox
				e.name === "QuotaExceededError" ||
				// Firefox
				e.name === "NS_ERROR_DOM_QUOTA_REACHED") &&
			// acknowledge QuotaExceededError only if there's something already stored
			storage &&
			storage.length !== 0
		);
	}
};

/**
 *
 * @param {HTMLButtonElement} button
 * @param {"B" | "E" | "N"} modus
 * @returns
 */
export async function download(button, modus) {
	setLoading(button.id);
	buttonDisable(true);
	const VorgabenGeld = JSON.parse(localStorage.getItem("VorgabenGeld"));
	Object.setPrototypeOf(VorgabenGeld, {
		getMonat: function (maxMonat) {
			let returnObjekt = this[1];
			const keys = Object.keys(this).map(Number);
			if (keys.length > 1 && maxMonat > 1 && Math.max(...keys.filter(key => key <= maxMonat)) > 1) {
				for (let monat = 2; monat <= maxMonat; monat++) {
					if (typeof this[monat] !== "undefined") returnObjekt = { ...returnObjekt, ...this[monat] };
				}
			}
			return returnObjekt;
		},
	});
	const Monat = +document.getElementById("Monat").value,
		Jahr = +document.getElementById("Jahr").value;
	let data = {
		VorgabenU: JSON.parse(localStorage.getItem("VorgabenU")),
		VorgabenGeld: VorgabenGeld.getMonat(Monat),
		Daten: {},
		Monat,
		Jahr,
	};

	switch (modus) {
		case "B":
			data.Daten.BZ = tableToArray("tableBZ");
			data.Daten.BE = tableToArray("tableBE");
			break;
		case "E":
			data.Daten.EWT = tableToArray("tableE");
			break;
		case "N":
			data.Daten.N = tableToArray("tableN");
			break;
	}

	try {
		console.time("download");
		const fetched = await FetchRetry(`download/${modus}`, data, "POST");
		if (fetched.statusCode != 200) {
			console.timeEnd("download");
			console.log("Fehler", fetched.message);
			createSnackBar({
				message: `Download fehlerhaft:<br/>${fetched.message}`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}
		downloadURI(`data:application/pdf;base64,${encodeURIComponent(fetched.data.data)}`, fetched.data.name);
		console.timeEnd("download");
	} catch (err) {
		console.log(err);
		return;
	} finally {
		buttonDisable(false);
		clearLoading(button.id);
	}

	function downloadURI(uri, name) {
		let link = document.createElement("a");
		link.download = name;
		link.href = uri;
		link.click();
	}
}

/**
 *
 * @param {HTMLButtonElement} button
 * @param {"B" | "E" | "N" | "Einstellungen"} [modus]
 * @returns
 */
export async function saveDaten(button, modus = "") {
	setLoading(button.id);
	buttonDisable(true);

	try {
		switch (modus) {
			case "B":
				saveTableData("tableBZ");
				saveTableData("tableBE");
				break;
			case "E":
				saveTableData("tableE");
				break;
			case "N":
				saveTableData("tableN");
				break;
			case "Einstellungen":
				saveEinstellungen();
				break;
			default:
				saveTableData("tableBZ");
				saveTableData("tableBE");
				saveTableData("tableE");
				saveTableData("tableN");
				saveEinstellungen();
				break;
		}

		const data = {
			BZ: JSON.parse(localStorage.getItem("dataBZ")),
			BE: JSON.parse(localStorage.getItem("dataBE")),
			E: JSON.parse(localStorage.getItem("dataE")),
			N: JSON.parse(localStorage.getItem("dataN")),
			User: JSON.parse(localStorage.getItem("VorgabenU")),
			Monat: localStorage.getItem("Monat"),
			Jahr: localStorage.getItem("Jahr"),
		};

		const fetched = await FetchRetry("saveData", data, "POST");

		if (fetched.statusCode != 200) {
			console.log("Fehler", fetched.message);
			let messages = JSON.parse(fetched.message);
			createSnackBar({
				message: "Speichern<br/>Es ist ein Fehler aufgetreten:" + messages.map(message => `<br/>- ${message}`),
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});

			return;
		}
		const dataResponded = fetched.data;
		console.log(dataResponded);

		let ftBZ;
		let ftBE;
		let ftE;
		let ftN;
		switch (modus) {
			case "B":
				ftBZ = document.getElementById("tableBZ").instance;
				console.log("save ", { ftBZ });
				ftBZ.rows.load(DataBZ(dataResponded.daten.datenBZ));

				ftBE = document.getElementById("tableBE").instance;
				console.log("save ", { ftBE });
				ftBE.rows.load(DataBE(dataResponded.daten.datenBE));

				saveTableData("tableBZ", ftBZ);
				saveTableData("tableBE", ftBE);
				localStorage.setItem("VorgabenU", JSON.stringify(dataResponded.user));
				localStorage.setItem("datenBerechnung", JSON.stringify(dataResponded.datenBerechnung));
				break;
			case "E":
				ftE = document.getElementById("tableE").instance;
				console.log("save ", { ftE });
				ftE.rows.load(DataE(dataResponded.daten.datenE));

				saveTableData("tableE", ftE);
				localStorage.setItem("VorgabenU", JSON.stringify(dataResponded.user));
				localStorage.setItem("datenBerechnung", JSON.stringify(dataResponded.datenBerechnung));
				break;
			case "N":
				ftN = document.getElementById("tableN").instance;
				console.log("save ", { ftN });
				ftN.rows.load(DataN(dataResponded.daten.datenN));

				saveTableData("tableN", ftN);
				localStorage.setItem("VorgabenU", JSON.stringify(dataResponded.user));
				localStorage.setItem("datenBerechnung", JSON.stringify(dataResponded.datenBerechnung));
				break;
			case "Einstellungen":
				generateEingabeMaskeEinstellungen(dataResponded.user);
				localStorage.setItem("VorgabenU", JSON.stringify(dataResponded.user));
				localStorage.setItem("datenBerechnung", JSON.stringify(dataResponded.datenBerechnung));
				break;
			default:
				generateTableBerechnung(dataResponded.datenBerechnung);
				generateEingabeMaskeEinstellungen(dataResponded.user);
				ftBZ = document.getElementById("tableBZ").instance;
				console.log("save ", { ftBZ });
				ftBZ.rows.load(DataBZ(dataResponded.daten.datenBZ));

				ftBE = document.getElementById("tableBE").instance;
				console.log("save ", { ftBE });
				ftBE.rows.load(DataBE(dataResponded.daten.datenBE));

				ftE = document.getElementById("tableE").instance;
				console.log("save ", { ftE });
				ftE.rows.load(DataE(dataResponded.daten.datenE));

				ftN = document.getElementById("tableN").instance;
				console.log("save ", { ftN });
				ftN.rows.load(DataN(dataResponded.daten.datenN));

				saveTableData("tableBZ", ftBZ);
				saveTableData("tableBE", ftBE);
				saveTableData("tableE", ftE);
				saveTableData("tableN", ftN);
				localStorage.setItem("VorgabenU", JSON.stringify(dataResponded.user));
				localStorage.setItem("datenBerechnung", JSON.stringify(dataResponded.datenBerechnung));
				break;
		}
		console.log("Erfolgreich gespeichert");
		createSnackBar({
			message: `Speichern<br/>Daten gespeichert`,
			status: "success",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
	} catch (err) {
		console.log(err);
		return;
	} finally {
		clearLoading(button.id);
		buttonDisable(false);
	}
}

/**
 *
 * @param {string} tableID
 * @param {import("./CustomTable.js").CustomTable} ft
 */
export function saveTableData(tableID, ft) {
	let data;
	switch (tableID) {
		case "tableBZ":
			data = tableToArray("tableBZ", ft);
			localStorage.setItem("dataBZ", JSON.stringify(data));
			break;
		case "tableBE":
			data = tableToArray("tableBE", ft);
			localStorage.setItem("dataBE", JSON.stringify(data));
			break;
		case "tableE":
			data = tableToArray("tableE", ft);
			localStorage.setItem("dataE", JSON.stringify(data));
			break;
		case "tableN":
			data = tableToArray("tableN", ft);
			localStorage.setItem("dataN", JSON.stringify(data));
	}
}

/**
 *
 *@param {string} tableID
 * @param {import("./CustomTable.js").CustomTable} ft
 * @returns
 */
export function tableToArray(tableID, ft) {
	if (!ft) ft = document.getElementById(tableID).instance;
	return ft.getRows().map(row => row.cells);
}

export function createEditorModalFooter(customButtons, submitText) {
	return () => {
		const modalFooter = document.createElement("div");
		modalFooter.className = "modal-footer";

		const btnSubmit = document.createElement("button");
		btnSubmit.type = "Submit";
		btnSubmit.className = "btn btn-primary";
		btnSubmit.arialabel = submitText ?? "Hinzufügen";
		btnSubmit.innerHTML = submitText ?? "Hinzufügen";
		modalFooter.appendChild(btnSubmit);

		if (customButtons) customButtons.forEach(button => modalFooter.appendChild(button));

		const btnClose = document.createElement("button");
		btnClose.type = "button";
		btnClose.className = "btn btn-secondary";
		btnSubmit.arialabel = "Abbrechen";
		btnClose.dataset.bsDismiss = "modal";
		btnClose.textContent = "Abbrechen";
		modalFooter.appendChild(btnClose);

		return modalFooter;
	};
}
