export function createModalBodyShowElement(options) {
	const divElement = document.createElement("div");
	divElement.className = options.divClass ?? "mb-1 row";

	const label = document.createElement("label");
	label.className = options.labelClass ?? "col-3 col-form-label text-wrap fw-bold";
	label.setAttribute("for", options.name);
	label.textContent = options.title;
	divElement.appendChild(label);

	const span = document.createElement("span");
	span.className = options.spanClass ?? "col-9 align-middle text-break my-auto";
	span.id = options.name;
	span.innerHTML = options.text ?? "&nbsp;";
	divElement.appendChild(span);

	return divElement;
}
export function createModalBodyInputElement(options) {
	const div = document.createElement("div");
	div.className = options.divClass;
	if (options.required) div.classList.add("required");

	const input = document.createElement("input");
	input.type = options.type;
	input.className = "form-control validate";
	input.id = options.name;
	input.name = options.title;
	input.value = options.value;
	if (options.required) input.required = true;
	if (options.disabled) input.disabled = true;
	if (options.min != undefined) input.min = options.min;
	if (options.max != undefined) input.max = options.max;
	if (options.eventListener) input.addEventListener("change", options.eventListener);

	div.appendChild(input);

	const label = document.createElement("label");
	label.htmlFor = options.name;
	label.textContent = options.title;
	div.appendChild(label);

	return div;
}
export function createModalBodySelectElement(options) {
	const div = document.createElement("div");
	div.className = options.divClass;
	if (options.required) div.classList.add("required");

	const select = document.createElement("select");
	select.className = "form-select validate";
	select.id = options.name;
	select.value = options.value;
	if (options.required) select.required = true;
	options.options.forEach(optionObject => {
		const option = document.createElement("option");
		option.value = optionObject.value;
		option.textContent = optionObject.text;
		if (optionObject.disabled) option.disabled = true;
		if (optionObject.selected && !options.value) option.selected = true;
		if (option.value === options.value) option.selected = true;

		select.appendChild(option);
	});
	div.appendChild(select);

	const label = document.createElement("label");
	label.className = "form-label";
	label.htmlFor = options.name;
	label.textContent = options.title;
	div.appendChild(label);
	return div;
}

export function createModalBodyCheckbox(options) {
	const div = document.createElement("div");
	div.className = options.checkClass;

	const input = document.createElement("input");
	input.type = "checkbox";
	input.className = "form-check-input";
	input.id = options.id;
	if (options.status) input.checked = true;
	if (options.disabled) input.disabled = true;
	if (options.eventListener != undefined) input.addEventListener("change", options.eventListener);
	div.appendChild(input);

	const label = document.createElement("label");
	label.className = "form-check-label";
	label.htmlFor = options.id;
	label.innerHTML = options.text;
	div.appendChild(label);
	return div;
}

