/**
 *
 * @param {string} btn
 */
export function setLoading(btn) {
	btn = document.getElementById(btn);
	if (!btn.dataset.normaltext) btn.dataset.normaltext = btn.innerHTML;
	if (!btn.dataset.loadingtext)
		btn.dataset.loadingtext = "<span class='spinner-grow spinner-grow-sm' role='status' aria-hidden='true'></span>";
	btn.setAttribute("disabled", true);
	btn.innerHTML = btn.dataset.loadingtext;
}

/**
 *
 * @param {string} btn
 */
export function clearLoading(btn) {
	btn = document.getElementById(btn);
	btn.innerHTML = btn.dataset.normaltext;
	btn.disabled = false;
}

export function DatenSortieren(daten) {
	daten.sort((x, y) => {
		const xp = x[0];
		const yp = y[0];
		if (xp < yp) {
			return xp == yp ? 0 : -1;
		} else {
			return xp == yp ? 0 : 1;
		}
	});
}

let REFRESHED = 0;

export let controller;
let signal;
newAbortControler();

export function newAbortControler() {
	controller = new AbortController();
	signal = controller.signal;
}

export async function FetchRetry(
	UrlPath,
	data,
	method = "GET",
	retry = 0,
	accessToken = localStorage.getItem("accessToken")
) {
	if (retry > 2) throw Error("Zu viele Tokenfehler");
	const lastServerContact = JSON.parse(localStorage.getItem("lastServerContact"));
	if (!lastServerContact || lastServerContact - Date.now() + 5 * 60 * 1000 < 0) {
		let statusSnackBar = createSnackBar({
			message: "Serververbindung wird aufgebaut. Bitte warten.",
			dismissible: false,
			status: "info",
			timeout: false,
			position: "br",
			fixed: true,
		});
		await fetch(`${API_URL}/`, { method: "GET", signal });
		await statusSnackBar.Close();
		localStorage.setItem("lastServerContact", JSON.stringify(Date.now()));
	}
	let headers = method === "GET" ? {} : { "Content-Type": "application/json" };
	if (accessToken) {
		if (!data || (data && !data.refreshToken)) accessToken = await checkJwt(accessToken);
		headers.Authorization = `Bearer ${accessToken}`;
	}
	const fetchObject = {
		mode: "cors",
		headers,
		method,
		signal,
		cache: "no-cache",
	};
	if (data) {
		fetchObject.body = JSON.stringify(data);
	}
	const response = await fetch(`${API_URL}/${UrlPath}`, fetchObject);
	const responded = await response.json();
	if (response.status == 401 && responded.message == "Token abgelaufen") {
		accessToken = await tokenErneuern(retry, undefined, accessToken);
		return await FetchRetry(UrlPath, data, method, retry + 1, accessToken);
	}
	responded.statusCode = response.status;
	return responded;
}

/**
 * @param {string} [accesstoken]
 */
async function checkJwt(accesstoken = localStorage.getItem("accessToken")) {
	let base64Url = accesstoken.split(".")[1];
	const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
	const jsonPayload = decodeURIComponent(
		window
			.atob(base64)
			.split("")
			.map(function (c) {
				return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
			})
			.join("")
	);
	const tokenPayload = JSON.parse(jsonPayload);
	const expirationTimeInSeconds = tokenPayload.exp * 1000;
	const now = new Date();
	if (expirationTimeInSeconds < now.getTime()) return await tokenErneuern();
	return accesstoken;
}

/**
 * @param {Function} callback
 * @param {*} args
 * @param {string} [refreshToken]
 * @param {string} [accessToken]
 * @returns
 */
async function tokenErneuern(
	retry,
	refreshToken = localStorage.getItem("refreshToken"),
	accessToken = localStorage.getItem("accessToken")
) {
	if (!refreshToken || !accessToken || REFRESHED > 2) {
		REFRESHED = 0;
		Logout();
		createSnackBar({
			message: `Login<br/>Fehlerhafte Anmeldung,</br> bitte Erneut anmelden!`,
			status: "error",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
		throw Error("Fehler bei Token erneuerung");
	}
	const responded = await FetchRetry("refreshToken", { refreshToken }, "POST", retry, accessToken);
	console.log(responded);
	if (responded.statusCode === 200) {
		REFRESHED++;
		let { accessToken, refreshToken } = responded.data;
		console.log({ accessToken, refreshToken });
		localStorage.setItem("accessToken", accessToken);
		localStorage.setItem("refreshToken", refreshToken);
		return accessToken;
	} else {
		Logout();
		createSnackBar({
			message: `Login<br/>Fehlerhafte Anmeldung,</br> bitte Erneut anmelden!`,
			status: "error",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
		throw Error("Fehler bei Token erneuerung");
	}
}

export function isObject(value) {
	return (
		typeof value === "object" && !Array.isArray(value) && Object.prototype.toString.call(value) === "[object Object]"
	);
}
