export class Column {
	/**
	 * @param {CustomTable} table - The CustomTable object
	 * @param {object} column
	 * @param {string} column.name
	 * @param {string} column.title
	 * @param {"xs" | "sm" | "md" | "lg" | "xl" | "xxl" } [column.breakpoints]
	 * @param {boolean} [column.sortable]
	 * @param {boolean} [column.sorted]
	 * @param {"ASC" | "DESC"} [column.direction]
	 * @param {string} [column.type]
	 * @param {function} [column.parser]
	 * @param {string[]} [column.classes]
	 * @param {boolean} [column.visible]
	 * @param {number} index
	 */
	constructor(table, column, index) {
		if (!column.name) throw Error("Name fehlt");
		if (!column.title && !column.editing) throw Error("Title fehlt");
		this.index = index;
		this.name = column.name;
		this.title = column.title;
		this.breakpoints = column.breakpoints;
		this.sortable = column.sortable;
		this.sorted = column.sorted;
		this.direction = column.direction;
		this.type = column.type;
		this.parser = column.parser;
		this.classes = column.classes;
		this.visible = column.visible;
		if (column.editing) this.editing = column.editing;
		this.CustomTable = table;
	}
}

export class Columns {
	/**
	 * @param {CustomTable} table - The table element
	 * @param {{
	 * 	name: string,
	 * 	title: string,
	 * 	breakpoints?: "xs" | "sm" | "md" | "lg" | "xl" | "xxl",
	 * 	sortable?: boolean,
	 * 	sorted?: boolean,
	 * 	direction?: "ASC" | "DESC",
	 * 	type?: string,
	 * 	parser?: function,
	 * 	classes?: string[],
	 * 	visible?: boolean,
	 * }[]} columns
	 */
	constructor(table, columns) {
		this.array = columns.map((column, index) => new Column(table, column, index));
		this.CustomTable = table;
	}
}

export class Row {
	/**
	 * @param {CustomTable} table
	 * @param {object} row
	 */
	constructor(table, row) {
		this.CustomTable = table;
		this.columns = table.columns;
		this.cells = row;
	}

	/** This function removes the current row from the array of rows, and then redraws the table. */
	deleteRow() {
		this.CustomTable.rows.array = this.CustomTable.rows.array.filter(row => row !== this);
		this.CustomTable.drawRows();
	}

	/**
	 * The function takes a value, sets the cells property to that value, and then calls the drawRows function.
	 * @param {object} value - The value of the cell.
	 */
	val(value) {
		this.cells = value;
		this.CustomTable.drawRows();
	}
}

export class Rows {
	array = [];
	/**
	 * @param {CustomTable} table
	 * @param {object[]} rows
	 */
	constructor(table, rows) {
		this.array = rows.map(row => new Row(table, row));
		this.CustomTable = table;
	}
	/**
	 * It adds a new row to the table.
	 * @param {object} value - The value to be added to the table.
	 */
	add(value) {
		this.array.push(new Row(this.CustomTable, value));
		this.CustomTable.drawRows();
	}

	/**
	 * The load function takes an array of objects and adds them to the CustomTable.
	 * @param {object[]} array - The array of data to load.
	 * @param {boolean} [add=false] - If true, the new rows will be added to the existing rows. If false, the
	 * existing rows will be replaced with the new rows.
	 */
	load(array, add = false) {
		if (!add) this.array.length = 0;
		array.forEach(row => this.array.push(new Row(this.CustomTable, row)));
		this.CustomTable.drawRows();
	}
}

export class CustomTable {
	rows = {};
	/**
	 * @param {string | HTMLTableElement} table
	 * @param {object} options
	 * @param {object[]} options.column
	 * @param {string} options.column.name
	 * @param {string} options.column.title
	 * @param {"xs" | "sm" | "md" | "lg" | "xl" | "xxl" } [options.column.breakpoints]
	 * @param {boolean} [options.column.sortable]
	 * @param {boolean} [options.column.sorted]
	 * @param {"ASC" | "DESC"} [options.column.direction]
	 * @param {string} [options.column.type]
	 * @param {function} [options.column.parser]
	 * @param {string[]} [options.column.classes]
	 * @param {boolean} [options.column.visible]
	 * @param {object[]} options.rows
	 * @param {string} [options.empty]
	 * @param {object} [options.sorting]
	 * @param {boolean} [options.sorting.enabled]
	 * @param {object} [options.editing]
	 * @param {boolean} [options.editing.enabled]
	 * @param {string} [options.editing.addText]
	 * @param {string} [options.editing.editText]
	 * @param {string} [options.editing.deleteText]
	 * @param {string} [options.editing.deleteAllText]
	 * @param {()=>{}} [options.editing.addRow]
	 * @param {(row: Row)=>{}} [options.editing.editRow]
	 * @param {(row: Row)=>{}} [options.editing.showRow]
	 * @param {(row: Row)=>{}} [options.editing.deleteRow]
	 * @param {()=>{}} [options.editing.deleteAllRows]
	 * @param {object} [options.editing.customButton]
	 * @param {string[]} [options.editing.customButton.classes]
	 * @param {string} [options.editing.customButton.text]
	 * @param {()=>{}} [options.editing.customButton.function]
	 * @param {string[]} [options.classes]
	 * @param {object} [options.customFunction]
	 * @param {function} [options.customFunction.beforeDraw]
	 * @param {function} [options.customFunction.beforeDraw]
	 * @param {function} [options.customFunction.beforeFooterDraw]
	 * @param {function} [options.customFunction.beforeFooterDraw]
	 * @param {function} [options.customFunction.beforeRowsDraw]
	 * @param {function} [options.customFunction.beforeRowsDraw]
	 * @param {function} [options.customFunction.beforeHeaderDraw]
	 * @param {function} [options.customFunction.beforeHeaderDraw]
	 * @returns {CustomTable}
	 */
	constructor(table, options) {
		if (typeof table === "string") {
			this.table = table;
			this.$el = document.querySelector(`#${table}`);
		} else if (table instanceof HTMLTableElement) {
			this.table = table.id;
			this.$el = table;
		} else {
			throw Error("Tabellen Fehler");
		}
		this.$el.instance = this;
		this.state = {};
		this.o = { breakpoints: { xs: 480, sm: 576, md: 768, lg: 992, xl: 1200, xxl: 1400 } };
		this.options = ApplyOptions(options);
		let self = this;

		let thead = self.$el.tHead,
			tbody = self.$el.tBodies,
			tfoot = self.$el.tFoot;
		self.$el.classList.add(...["customtable", ...self.options.classes]);
		if (!thead || thead.length === 0) self.$el.createTHead();
		if (!tbody || tbody.length === 0) self.$el.createTBody();
		if (!tfoot || tfoot.length === 0) self.$el.createTFoot();

		self.state = setState();

		if (self.state.editing) {
			const editingRow = {
				enabled: self.options.editing.enabled,
				name: "editing",
				title: "",
				type: "editing",
				breakpoints: "",
				sortable: false,
				editing: self.options.editing,
				classes: ["customtable-editing"],
				visible: true,
			};
			const maxBreakpoint = this.maxBreakpoint(self.options.columns);
			const breakpoint = Object.entries(this.o.breakpoints).find(value => value[1] === maxBreakpoint)[0];
			editingRow.classes.push(`customtable-toggle-${breakpoint}`);
			self.options.columns.unshift(editingRow);
		}
		self.columns = new Columns(self, self.options.columns);
		self.rows = new Rows(self, self.options.rows);

		self.draw();

		if (self.$el.tFoot.length === 0) self.$el.tHead.deleteTFoot();
		if (self.$el.tHead.length === 0) self.$el.tHead.deleteTHead();

		console.log(this);
		return this;

		/**
		 * It takes an object as an argument and returns an object with the same properties as the argument,
		 * but with default values for the properties that are not defined in the argument
		 * @param options - The options object that is passed to the constructor.
		 * @returns
		 */
		function ApplyOptions(options) {
			if (!options.columns) throw Error("Spalten fehlen");

			return {
				columns: options.columns.map(column => {
					if (!column.name) throw Error("Spalten Name fehlt");
					return {
						name: column.name,
						title: column.title ?? "",
						breakpoints: column.breakpoints ?? null,
						sortable: column.sortable ?? false,
						sorted: column.sorted ?? false,
						direction: column.direction ?? null,
						type: column.type ?? "text",
						parser: column.parser ?? _parser,
						classes: column.classes ?? [],
						visible: column.visible ?? true,
					};
				}),
				rows: options.rows ?? [],
				empty: options.empty ?? "Keine Daten gefunden",
				sorting: {
					enabled: options.sorting.enabled ?? false,
				},
				editing: {
					enabled: options.editing.enabled ?? false,
					addText: options.editing.addText ?? "Neue Zeile",
					editText:
						options.editing.editText ?? '<span class="material-icons-round small-icons" aria-hidden="true">edit</span>',
					deleteText:
						options.editing.deleteText ?? '<span class="material-icons-round small-icons" aria-hidden="true">delete</span>',
					deleteAllText: options.editing.deleteAllText ?? "Alle Zeilen löschen",
					addRow: options.editing.addRow ?? function () {},
					editRow: options.editing.editRow ?? function () {},
					showRow: options.editing.showRow ?? function () {},
					deleteRow: options.editing.deleteRow ?? function () {},
					deleteAllRows: options.editing.deleteAllRows ?? self.#deleteAllRows,
					customButton: options.editing.customButton ?? null,
				},
				classes: options.classes ?? [],
				customFunction: options.customFunction ?? null,
			};
		}

		/**
		 * _parser() is a function that takes a value and returns a value based on the type of the value.
		 * @param {string | number} value - The value of the cell.
		 * @returns The value of the input field.
		 */
		function _parser(value) {
			switch (this.type) {
				case "text":
					return value;
				case "number":
					return value + "";
				case "time":
					return value.length > 0 ? value : "--:--";
				default:
					return value;
			}
		}

		/**
		 * If the sorting property of the options object is truthy, then return the value of the sorting
		 * property of the options object, otherwise return null.
		 * @returns The return value is an object with two properties: sorting and editing.
		 */
		function setState() {
			return {
				sorting: self.options.sorting?.enabled ?? null,
				editing: self.options.editing?.enabled ?? null,
			};
		}
	}

	/**
	 * It returns an array of the rows in the table.
	 * @returns {object[]} An array of rows.
	 */
	getRows() {
		return this.rows.array;
	}

	/**
	 * It returns an array of arrays, where each inner array is an array of the values of the cells of a
	 * row
	 * @returns An array of arrays.
	 */
	getArray() {
		return this.rows.array.map(row => {
			return Object.values(row.cells);
		});
	}

	/**
	 * It creates all elements for the table
	 */
	draw() {
		if (this.options.customFunction?.beforeDraw) this.options.customFunction.beforeDraw(this);
		this.drawHeader();
		this.drawFooter();
		this.drawRows();
		if (this.options.customFunction?.afterDraw) this.options.customFunction.afterDraw(this);
	}

	/**
	 * It creates a footer for the table
	 */
	drawFooter() {
		if (this.options.customFunction?.beforeDrawFooter) this.options.customFunction.beforeDrawFooter(this);

		let self = this;
		const tfoot = self.$el.tFoot;
		tfoot.innerHTML = "";
		if (self.state.editing) {
			let tr = document.createElement("tr");
			tr.classList.add("customtable-editing");

			let td = document.createElement("td");
			td.colSpan = self.columns.array.length + 1;

			let divFooter = document.createElement("div");
			divFooter.classList.add("justify-content-sm-evenly");

			let buttonAdd = createButton(["btn", "btn-primary"], self.options.editing.addText, self.options.editing.addRow);
			let buttonDeleteAlle = createButton(
				["btn", "btn-danger"],
				self.options.editing.deleteAllText,
				self.options.editing.deleteAllRows
			);

			divFooter.appendChild(buttonAdd);
			if (self.options.editing.customButton?.length > 0) {
				self.options.editing.customButton.forEach(button => {
					let customButton = createButton(button.classes, button.text, button.function);
					divFooter.appendChild(customButton);
				});
			}

			divFooter.appendChild(buttonDeleteAlle);

			td.appendChild(divFooter);
			tr.appendChild(td);
			tfoot.appendChild(tr);
		}
		if (this.options.customFunction?.afterDrawFooter) this.options.customFunction.afterDrawFooter(this);

		function createButton(classes = ["btn", "btn-primary"], text = "Button", eventlistener = () => {}) {
			let button = document.createElement("button");
			button.classList.add(...classes);
			button.innerText = text;
			button.title = text;
			button.addEventListener("click", eventlistener);
			return button;
		}
	}

	/**
	 * It draws the rows of the table
	 */
	drawRows() {
		if (this.options.customFunction?.beforeDrawRows) this.options.customFunction.beforeDrawRows(this);

		let self = this;
		const thead = self.$el.tHead;
		const tbody = self.$el.tBodies[0];
		const tfoot = self.$el.tFoot;
		tbody.innerHTML = "";
		const sortedColumn = self.columns.array.find(column => column.sorted);
		if (sortedColumn) self.#sort(sortedColumn.index, sortedColumn.direction);

		if (self.rows.array.length > 0) {
			for (const row of self.rows.array) {
				let tr = document.createElement("tr");
				row.columns.array.forEach(column => {
					if (!column.visible) {
						return;
					}
					let td = document.createElement("td");
					if (column.editing) {
						let divBtnGroup = document.createElement("div");
						divBtnGroup.classList.add("btn-group", "btn-group-sm");
						divBtnGroup.setAttribute("role", "group");

						const createButton = (classList, text, eventListener, title = "button") => {
							const button = document.createElement("button");
							button.classList.add(...classList);
							button.innerHTML = text;
							button.title = title;
							button.addEventListener("click", event => {
								event.stopPropagation();
								eventListener(row);
							});
							return button;
						};

						const buttonEdit = createButton(
							["btn", "btn-outline-primary"],
							column.editing.editText,
							self.options.editing.editRow,
							"edit"
						);
						const buttonDelete = createButton(
							["btn", "btn-outline-danger"],
							column.editing.deleteText,
							self.options.editing.deleteRow,
							"delete"
						);

						divBtnGroup.append(buttonEdit);
						divBtnGroup.append(buttonDelete);
						td.appendChild(divBtnGroup);
					} else {
						td.innerHTML = column.parser(row.cells[column.name]);
					}
					if (column.breakpoints) td.dataset.breakpoints = column.breakpoints;
					if (column.classes.length > 0) td.classList.add(...column.classes);
					tr.append(td);
				});
				tr.addEventListener("click", event => {
					if (event.view.innerWidth > self.maxBreakpoint()) return;
					event.stopPropagation();
					self.options.editing.showRow(row);
				});
				tr.data = row;
				row.$el = tr;
				tbody.appendChild(tr);
			}
			thead.style.display = "";
			tfoot.querySelector(".btn-danger").style.display = "";
		} else {
			let tr = document.createElement("tr");
			tr.classList.add("customtable-empty");
			let td = document.createElement("td");
			td.colSpan = self.columns.array.length + 1;
			td.innerText = self.options.empty;
			tr.appendChild(td);
			tbody.appendChild(tr);

			thead.style.display = "none";
			tfoot.querySelector(".btn-danger").style.display = "none";
		}
		if (this.options.customFunction?.afterDrawRows) this.options.customFunction.afterDrawRows(this);
	}

	/**
	 * It draws the header of the table
	 */
	drawHeader() {
		if (this.options.customFunction?.beforeDrawHeader) this.options.customFunction.beforeDrawHeader(this);

		let self = this;
		const thead = self.$el.tHead;
		thead.innerHTML = "";
		let tr = document.createElement("tr");
		tr.classList.add("customtable-header", "table-primary");
		self.columns.array.forEach(column => {
			if (!column.visible) return;
			let th = document.createElement("th");
			if (column.classes.length > 0) th.classList.add(column.classes);
			th.innerHTML = column.title;
			if (column.breakpoints) th.dataset.breakpoints = column.breakpoints;
			if (self.state.sorting && column.sortable) handleSortable(th, column);
			column.$el = th;
			tr.appendChild(th);
		});

		thead.appendChild(tr);
		if (this.options.customFunction?.afterDrawHeader) this.options.customFunction.afterDrawHeader(this);

		function handleSortable(th, column) {
			th.classList.add("customtable-sortable");
			let span = document.createElement("span");
			span.classList.add("customtableIcon");
			handleSorted(column, th, span);
			th.append(span);
			th.addEventListener("click", event => {
				const element = event.target.closest("th");
				self.#onSortClicked(element);
			});
		}

		function handleSorted(column, th, span) {
			if (!column.sorted) return span.classList.add("customtable-sort");
			let direction = column.direction.toLowerCase();
			self.#sort(column.index, direction.toUpperCase());
			th.classList.add(`customtable-${direction}`);
			span.classList.add(direction == "asc" ? "customtable-sort-asc" : "customtable-sort-desc");
		}
	}

	#deleteAllRows() {
		this.rows.array.length = 0;
		this.draw();
	}

	#sort(columnIndex, direction) {
		const directionOrder = {
			ASC: [1, -1],
			DESC: [-1, 1],
			default: [1, -1],
		};
		let rows = this.rows.array;
		let Sorter = (a, b) => {
			let aValue = a.cells[a.columns.array[columnIndex].name];
			let bValue = b.cells[b.columns.array[columnIndex].name];
			return (
				"string" == typeof aValue && (aValue = aValue.toLowerCase()),
				"string" == typeof bValue && (bValue = bValue.toLowerCase()),
				bValue.localeCompare(aValue, undefined, { numeric: true }) *
					(directionOrder[direction] || directionOrder.default)[1]
			);
		};

		rows.sort(Sorter);
	}

	#onSortClicked(element) {
		const column = this.columns.array.find(column => column.$el == element);
		const direction = column.direction === "ASC" ? "DESC" : "ASC";
		this.columns.array.forEach(column => {
			column.sorted = false;
			column.direction = null;
		});
		column.direction = direction;
		column.sorted = true;
		this.draw();
	}

	/**
	 * It takes an array of objects, and returns an array of unique values from the "breakpoints" property
	 * of each object.
	 * @param {} array - The array of columns to get the breakpoints from. If not provided, it will use the
	 * columns.array property.
	 * @returns {string[]} An array of unique breakpoints.
	 */
	breakpoints(array) {
		if (!array) array = this.columns.array;
		return Array.from(
			new Set([].concat(...array.filter(column => column.breakpoints).map(column => column.breakpoints.split(" "))))
		);
	}

	/**
	 * It returns the largest breakpoint value from an array of breakpoint names
	 * @param {CustomTable.options.columns} array - The array of items to be sorted.
	 * @returns {number} The maximum breakpoint value.
	 */
	maxBreakpoint(array) {
		return Math.max(...this.breakpoints(array).map(breakpoint => this.o.breakpoints[breakpoint]));
	}
}

/**
 * @param {string | HTMLTableElement} table
 * @param {object} options
 * @param {object[]} options.columns
 * @param {string} options.columns.name
 * @param {string} options.columns.title
 * @param {"xs" | "sm" | "md" | "lg" | "xl" | "xxl" } [options.columns.breakpoints]
 * @param {boolean} [options.columns.sortable]
 * @param {boolean} [options.columns.sorted]
 * @param {"ASC" | "DESC"} [options.columns.direction]
 * @param {string} [options.columns.type]
 * @param {function} [options.columns.parser]
 * @param {string[]} [options.columns.classes]
 * @param {boolean} [options.columns.visible]
 * @param {object[]} options.rows
 * @param {string} [options.empty]
 * @param {object} [options.sorting]
 * @param {boolean} [options.sorting.enabled]
 * @param {object} [options.editing]
 * @param {boolean} [options.editing.enabled]
 * @param {string} [options.editing.addText]
 * @param {string} [options.editing.editText]
 * @param {string} [options.editing.deleteText]
 * @param {string} [options.editing.deleteAllText]
 * @param {()=>{}} [options.editing.addRow]
 * @param {(row: Row)=>{}} [options.editing.editRow]
 * @param {(row: Row)=>{}} [options.editing.showRow]
 * @param {(row: Row)=>{}} [options.editing.deleteRow]
 * @param {()=>{}} [options.editing.deleteAllRows]
 * @param {object} [options.editing.customButton]
 * @param {string[]} [options.editing.customButton.classes]
 * @param {string} [options.editing.customButton.text]
 * @param {()=>{}} [options.editing.customButton.function]
 * @param {string[]} [options.classes]
 * @param {object} [options.customFunction]
 * @param {function} [options.customFunction.beforeDraw]
 * @param {function} [options.customFunction.beforeDraw]
 * @param {function} [options.customFunction.beforeFooterDraw]
 * @param {function} [options.customFunction.beforeFooterDraw]
 * @param {function} [options.customFunction.beforeRowsDraw]
 * @param {function} [options.customFunction.beforeRowsDraw]
 * @param {function} [options.customFunction.beforeHeaderDraw]
 * @param {function} [options.customFunction.beforeHeaderDraw]
 * @returns {CustomTable}
 */
export function createCustomTable(table, options) {
	return new CustomTable(table, options);
}
