import { clearLoading } from "./Core.js";

window.addEventListener("load", () => {
	if (
		localStorage.getItem("datenBerechnung") &&
		localStorage.getItem("VorgabenU") &&
		localStorage.getItem("VorgabenGeld")
	)
		generateTableBerechnung(
			JSON.parse(localStorage.getItem("datenBerechnung")),
			JSON.parse(localStorage.getItem("VorgabenGeld"))
		);
});

/**
 * It takes a JSON object and generates a table from it
 * @param datenBerechnung
 * @param [datenGeld]
 * @returns nothing.
 */
export function generateTableBerechnung(datenBerechnung, datenGeld = JSON.parse(localStorage.getItem("VorgabenGeld"))) {
	if (datenBerechnung === true) return clearLoading("btnNeuBerech");
	Object.setPrototypeOf(datenGeld, {
		getMonat: function (maxMonat) {
			let returnObjekt = this[1];
			const keys = Object.keys(this).map(Number);
			if (keys.length > 1 && maxMonat > 1 && Math.max(...keys.filter(key => key <= maxMonat)) > 1) {
				for (let monat = 2; monat <= maxMonat; monat++) {
					if (typeof this[monat] !== "undefined") returnObjekt = { ...returnObjekt, ...this[monat] };
				}
			}
			return returnObjekt;
		},
	});

	const tarif_beamter = JSON.parse(localStorage.getItem("VorgabenU")).pers.TB;
	const berechnung = Array.from({ length: 12 }, () => []);
	const tbody = document.getElementById("tbodyBerechnung");

	tbody.innerHTML =
		'<tr><th rowspan="2">Bereitschaftszeiten</th></tr>' +
		"<tr></tr>" +
		"<tr><th>Bereitschaftszulage</th></tr>" +
		"<tr><th>LRE 1</th></tr>" +
		"<tr><th>LRE 2</th></tr>" +
		"<tr><th>LRE 3</th></tr>" +
		"<tr><th>Privat-PKW</th></tr>" +
		"<tr><th>Summe Bereitschaft</th></tr>" +
		'<tr><th><table class="table table-borderless m-0"><tbody>' +
		'			<tr><td class="py-0">Anzahl der</td><td class="py-0">>8</td></tr>' +
		'			<tr><td class="py-0">Abwesenheiten nach</td><td class="py-0">>14</td>' +
		'			</tr><tr><td class="py-0">FGr-TV / LfTV / RVB</td><td class="py-0">>24</td></tr>' +
		"		 </tbody></table></th></tr>" +
		'<tr><th><table class="table table-borderless m-0"><tbody>' +
		'			<tr><td class="py-0">steuerfreie Abwesen-</td><td class="py-0">>8</td></tr>' +
		'			<tr><td class="py-0">heiten § 9 EStG</td><td class="py-0">>14</td>' +
		"		 </tbody></table></th></tr>" +
		"<tr><th>Summe EWT</th></tr>" +
		"<tr><th>Summe Nebenbezüge</th></tr>" +
		"<tr><th>Summe Gesamt</th></tr>";

	const nullParser = value => (value === 0 ? "&nbsp;" : value);

	Array.from(tbody.children).forEach((row, index) => {
		for (const [Monat, datenBerechnungItem] of Object.entries(datenBerechnung)) {
			const monat = Monat - 1;
			let td = document.createElement("td"),
				privatPKW;

			switch (index) {
				case 0:
					if (datenBerechnungItem.B.B !== 0) {
						td.textContent = datenBerechnungItem.B.B;
					}
					break;
				case 1:
					if (datenBerechnungItem.B.B !== 0) {
						td.textContent =
							tarif_beamter === "Tarifkraft"
								? time_convert(datenBerechnungItem.B.B)
								: Math.round((datenBerechnungItem.B.B - 600) / 8 / 60, 0);
					}
					break;
				case 2:
					if (datenBerechnungItem.B.B !== 0) {
						berechnung[monat][0] =
							tarif_beamter === "Tarifkraft"
								? Math.round(datenBerechnungItem.B.B / 60, 0) * datenGeld.getMonat(Monat)[tarif_beamter]
								: Math.round((datenBerechnungItem.B.B - 600) / 8 / 60, 0) * datenGeld.getMonat(Monat)[tarif_beamter];

						td.textContent = berechnung[monat][0].toLocaleString("de-DE", {
							style: "currency",
							currency: "EUR",
						});
					}
					break;
				case 3:
					if (datenBerechnungItem.B.L1 !== 0) {
						berechnung[monat][0] += Math.round(datenBerechnungItem.B.L1, 0) * datenGeld.getMonat(Monat).LRE1;
						td.textContent = (Math.round(datenBerechnungItem.B.L1, 0) * datenGeld.getMonat(Monat).LRE1).toLocaleString(
							"de-DE",
							{
								style: "currency",
								currency: "EUR",
							}
						);
					}
					break;
				case 4:
					if (datenBerechnungItem.B.L2 !== 0) {
						berechnung[monat][0] += Math.round(datenBerechnungItem.B.L2, 0) * datenGeld.getMonat(Monat).LRE2;
						td.textContent = (Math.round(datenBerechnungItem.B.L2, 0) * datenGeld.getMonat(Monat).LRE2).toLocaleString(
							"de-DE",
							{
								style: "currency",
								currency: "EUR",
							}
						);
					}
					break;
				case 5:
					if (datenBerechnungItem.B.L3 !== 0) {
						berechnung[monat][0] += Math.round(datenBerechnungItem.B.L3, 0) * datenGeld.getMonat(Monat).LRE3;
						td.textContent = (Math.round(datenBerechnungItem.B.L3, 0) * datenGeld.getMonat(Monat).LRE3).toLocaleString(
							"de-DE",
							{
								style: "currency",
								currency: "EUR",
							}
						);
					}
					break;
				case 6:
					if (datenBerechnungItem.B.K !== 0) {
						privatPKW =
							Math.round(datenBerechnungItem.B.K, 0) *
							(tarif_beamter === "Tarifkraft"
								? datenGeld.getMonat(Monat).PrivatPKWTarif
								: datenGeld.getMonat(Monat).PrivatPKWBeamter);

						berechnung[monat][0] += privatPKW;
						td.textContent = privatPKW.toLocaleString("de-DE", {
							style: "currency",
							currency: "EUR",
						});
					}
					break;
				case 7:
					if (berechnung[monat].length !== 0) {
						td.textContent = berechnung[monat][0].toLocaleString("de-DE", {
							style: "currency",
							currency: "EUR",
						});
					} else if (!berechnung[monat][0]) {
						berechnung[monat][0] = 0;
					}
					break;
				case 8:
					if (tarif_beamter === "Tarifkraft") {
						if (datenBerechnungItem.E.A8 !== 0) {
							berechnung[monat][1] = datenBerechnungItem.E.A8 * datenGeld.getMonat(Monat).TE8;
						}
						if (datenBerechnungItem.E.A14 !== 0) {
							berechnung[monat][1] += datenBerechnungItem.E.A14 * datenGeld.getMonat(Monat).TE14;
						}
						if (datenBerechnungItem.E.A24 !== 0) {
							berechnung[monat][1] += datenBerechnungItem.E.A24 * datenGeld.getMonat(Monat).TE24;
						}
					}
					if (datenBerechnungItem.E.A8 > 0 || datenBerechnungItem.E.A14 > 0 || datenBerechnungItem.E.A24 > 0) {
						td.innerHTML =
							`${nullParser(datenBerechnungItem.E.A8)} <br />` +
							`${nullParser(datenBerechnungItem.E.A14)} <br />` +
							`${nullParser(datenBerechnungItem.E.A24)}`;
					}
					break;

				case 9:
					if (tarif_beamter !== "Tarifkraft") {
						if (datenBerechnungItem.E.S8 !== 0) {
							berechnung[monat][1] = datenBerechnungItem.E.S8 * datenGeld.getMonat(Monat).BE8;
						}
						if (datenBerechnungItem.E.S14 !== 0) {
							berechnung[monat][1] += datenBerechnungItem.E.S14 * datenGeld.getMonat(Monat).BE14;
						}
					}
					if (datenBerechnungItem.E.S8 > 0 || datenBerechnungItem.E.S14 > 0) {
						td.innerHTML = `${nullParser(datenBerechnungItem.E.S8)} <br /> ${nullParser(datenBerechnungItem.E.S14)}`;
					}
					row.appendChild(td);
					break;

				case 10:
					if (berechnung[monat].length > 1) {
						td.textContent = berechnung[monat][1].toLocaleString("de-DE", {
							style: "currency",
							currency: "EUR",
						});
					} else if (!berechnung[monat][1]) {
						berechnung[monat][1] = 0;
					}
					break;
				case 11:
					if (datenBerechnungItem.N.F === 0) {
						berechnung[monat][2] = 0;
					} else {
						if (tarif_beamter !== "Tarifkraft") {
							berechnung[monat][2] = 0;
						} else {
							berechnung[monat][2] = datenBerechnungItem.N.F * datenGeld.getMonat(Monat).Fahrentsch;
						}
						td.textContent = berechnung[monat][2].toLocaleString("de-DE", {
							style: "currency",
							currency: "EUR",
						});
					}
					break;
				case 12:
					if (berechnung[monat].length !== 0 && (berechnung[monat][0] || berechnung[monat][1] || berechnung[monat][2])) {
						td.textContent = (
							Number(berechnung[monat][0]) +
							Number(berechnung[monat][1]) +
							Number(berechnung[monat][2])
						).toLocaleString("de-DE", { style: "currency", currency: "EUR" });
					}
					break;
			}
			row.appendChild(td);
		}
	});

	function time_convert(num) {
		const hours = Math.floor(num / 60);
		const minutes = Math.round(num % 60, 0);
		return `${hours}:${minutes.toString().padStart(2, "0")}`;
	}
}
