import { Modal } from "bootstrap";
import dayjs from "dayjs";
import "dayjs/locale/de";
import arraySupport from "dayjs/plugin/arraySupport";
import customParseFormat from "dayjs/plugin/customParseFormat";
import duration from "dayjs/plugin/duration";
import isoWeek from "dayjs/plugin/isoWeek";
import LocalizedFormat from "dayjs/plugin/LocalizedFormat";
import {
	buttonDisable,
	createEditorModalFooter,
	createModal,
	createModalBodyCheckbox,
	createModalBodyInputElement,
	createModalBodySelectElement,
	createModalBodyShowElement,
	createModalBodySpanElement,
	createModalBodyTitelElement,
	createShowModalFooter,
	DatenSortieren,
	download,
	isObject,
	saveDaten,
	saveTableData,
	tableToArray,
} from "./Core.js";
import { createCustomTable, CustomTable, Row } from "./CustomTable";
import { createSnackBar } from "./js-snackbar";
dayjs.locale("de");
dayjs.extend(arraySupport);
dayjs.extend(customParseFormat);
dayjs.extend(LocalizedFormat);
dayjs.extend(isoWeek);
dayjs.extend(duration);

window.addEventListener("load", () => {
	const berechnenParser = value => {
			return value
				? '<div class="form-check form-switch"><input type="checkbox" class="row-checkbox form-check-input" checked></div>'
				: '<div class="form-check form-switch"><input type="checkbox" class="row-checkbox form-check-input"></div>';
		},
		schichtParser = value => {
			switch (value) {
				case "T":
					return "Tag";
				case "N":
					return "Nacht";
				case "BN":
					return "<span class='SchichtBereitschaft'>Bereitschaft<br>+ Nacht</span>";
				case "S":
					return "Sonder";
				default:
					return "";
			}
		},
		ftE = createCustomTable("tableE", {
			columns: [
				{ name: "tagE", title: "Tag", sortable: true, sorted: true, direction: "ASC" },
				{ name: "eOrtE", title: "Einsatzort", classes: ["custom-text-truncate"], type: "text" },
				{ name: "schichtE", title: "Schicht", parser: schichtParser, type: "time" },
				{ name: "abWE", title: "Ab Wohnung", breakpoints: "xl", type: "time" },
				{ name: "beginE", title: "Arbeitszeit Von", breakpoints: "md", type: "time" },
				{ name: "ab1E", title: "Ab 1.Tgk.-St.", breakpoints: "lg", type: "time" },
				{ name: "anEE", title: "An Einsatzort", breakpoints: "lg", type: "time" },
				{ name: "abEE", title: "Ab Einsatzort", breakpoints: "lg", type: "time" },
				{ name: "an1E", title: "An 1.Tgk.-St.", breakpoints: "lg", type: "time" },
				{ name: "endeE", title: "Arbeitszeit Bis", breakpoints: "md", type: "time" },
				{ name: "anWE", title: "An Wohnung", breakpoints: "xl", type: "time" },
				{ name: "berechnen", title: "Berechnen?", parser: berechnenParser, breakpoints: "xxl" },
			],
			rows: DataE(),
			sorting: { enabled: true },
			editing: {
				enabled: true,
				addRow: () => {
					const $modal = createEditorModalEWT(ftE, "Anwesenheit hinzufügen");
					$modal.row = ftE;
					new Modal($modal).show();
				},
				editRow: row => {
					const $modal = createEditorModalEWT(row, "Anwesenheit bearbeiten");
					$modal.row = row;
					new Modal($modal).show();
				},
				showRow: row => {
					const $modal = createShowModalEWT(row, "Anwesenheit anzeigen");
					$modal.row = row;
					new Modal($modal).show();
				},
				deleteRow: row => {
					row.deleteRow();
					saveTableData("tableE", ftE);
				},
				deleteAllRows: () => {
					if (confirm("Möchtest du wirklich alle Zeilen löschen?", "JA", "NEIN")) {
						ftE.rows.load([]);
						buttonDisable(false);
						localStorage.setItem("dataE", JSON.stringify([]));
					}
				},
				customButton: [
					{
						classes: ["btn", "btn-secondary"],
						text: "Alle Zeiten entfernen",
						function: () => {
							if (confirm("Möchtest du wirklich alle Zeiten entfernen?\n(bei Zeilen die auch berechnet werden)")) {
								let rows = [...ftE.rows.array];
								let newRows = [];
								rows.forEach(row => {
									if (!row.cells.berechnen) return newRows.push(row.cells);
									row.cells.abWE = "";
									row.cells.ab1E = "";
									row.cells.anEE = "";
									row.cells.beginE = "";
									row.cells.endeE = "";
									row.cells.abEE = "";
									row.cells.an1E = "";
									row.cells.anWE = "";
									newRows.push(row.cells);
								});
								ftE.rows.load(newRows);
								saveTableData("tableE", ftE);
							}
						},
					},
				],
			},
			customFunction: {
				afterDrawRows: ftE => {
					addEventlistenerToggleBerechnen(ftE);
				},
			},
		});

	document.querySelector("#btnZb").addEventListener("click", () => {
		ewtBerechnen(
			localStorage.getItem("Monat"),
			localStorage.getItem("Jahr"),
			localStorage.getItem("dataE"),
			localStorage.getItem("VorgabenU")
		);
	});
	document.querySelector("#btnSaveE").addEventListener("click", () => {
		saveDaten(document.querySelector("#btnSaveE"));
	});
	document.querySelector("#btnDownloadE").addEventListener("click", () => {
		download(document.querySelector("#btnDownloadE"), "E");
	});
	document.querySelector("#btnESEE").addEventListener("click", createAddModalEWT);
});

function addEventlistenerToggleBerechnen(ftE) {
	document.querySelectorAll("#tableE .row-checkbox").forEach(element =>
		element.addEventListener("click", e => {
			e.stopPropagation();

			const row = e.target.closest("tr").data;
			let newValues = row.cells;
			newValues.berechnen = e.target.checked;

			row.val(newValues);

			saveTableData("tableE", ftE);
		})
	);
}

function createAddModalEWT() {
	const { modal, form } = createModal(
		"Neue Anwesenheit eingeben",
		true,
		"sm",
		createBodyElement,
		createEditorModalFooter(),
		SubmitEventListener
	);
	naechsterTag("");
	new Modal(modal).show();

	function createBodyElement() {
		const vorgabenU = JSON.parse(localStorage.getItem("VorgabenU"));

		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row g-2";

		const datum = dayjs([+localStorage.getItem("Jahr"), +localStorage.getItem("Monat") - 1, dayjs().date()]);

		const naechsterTagWrapper = document.createElement("div");
		const btnNaechsterTag = document.createElement("button");
		btnNaechsterTag.className = "btn btn-secondary btn-lg text-start col-12";
		btnNaechsterTag.type = "button";
		btnNaechsterTag.id = "btnNaechsterTag";
		btnNaechsterTag.textContent = "+1 Tag";
		btnNaechsterTag.addEventListener("click", e => {
			e.preventDefault();
			naechsterTag();
		});

		naechsterTagWrapper.appendChild(btnNaechsterTag);
		modalBody.appendChild(naechsterTagWrapper);

		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating",
				title: "Tag",
				name: "tagE",
				value: "",
				type: "date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
			})
		);

		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating",
				title: "Einsatzort",
				name: "EOrt",
				value: null,
				required: false,
				options: [
					{ value: "", text: "", selected: true },
					...vorgabenU.fZ.map(ort => {
						return {
							value: ort.key,
							text: ort.key,
						};
					}),
				],
			})
		);
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating",
				title: "Schicht",
				name: "Schicht",
				value: null,
				options: [
					{ value: "T", text: "Tag", selected: true },
					{ value: "N", text: "Nacht" },
					{ value: "BN", text: "Nacht/Bereitschaft" },
					{ value: "S", text: "Sonder" },
				],
				required: true,
			})
		);

		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch mt-3",
				id: "berechnen1",
				text: "Berechnen",
				status: true,
			})
		);

		return modalBody;
	}

	function SubmitEventListener() {
		return e => {
			if (form.checkValidity && !form.checkValidity()) return;
			e.preventDefault();

			addEWTtag();

			saveTableData("tableE", document.querySelector("#tableE").CustomTable);
		};
	}
}
function createEditorModalEWT(row, titel) {
	const { modal, form } = createModal(
		titel,
		true,
		"fullscreen-sm-down",
		createBodyElement,
		createEditorModalFooter(customFooterButtton(), row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);

	return modal;

	function createBodyElement() {
		const vorgabenU = JSON.parse(localStorage.getItem("VorgabenU"));

		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		let datum;
		const Monat = +localStorage.getItem("Monat");
		const Jahr = +localStorage.getItem("Jahr");
		if (row instanceof Row) {
			datum = dayjs([Jahr, Monat - 1, row.cells.tagE]);
		} else if (row instanceof CustomTable) {
			datum = dayjs([Jahr, Monat - 1, dayjs().date()]);
		} else throw Error("unbekannter Fehler");
		let column;

		column = row.columns.array.find(column => column.name === "tagE");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-5 pb-3",
				title: column.title,
				name: column.name,
				value: datum.format("YYYY-MM-DD"),
				type: "date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
			})
		);

		column = row.columns.array.find(column => column.name === "eOrtE");
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col-12 col-sm-7 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || null,
				required: false,
				options: [
					{ value: "", text: "", selected: true },
					...vorgabenU.fZ.map(ort => {
						return {
							value: ort.key,
							text: ort.key,
						};
					}),
				],
			})
		);

		column = row.columns.array.find(column => column.name === "schichtE");
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col-12 col-sm-7 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || null,
				required: true,
				options: [
					{ value: "T", text: "Tag", selected: true },
					{ value: "N", text: "Nacht" },
					{ value: "BN", text: "Nacht/Bereitschaft" },
					{ value: "S", text: "Sonder" },
				],
			})
		);
		column = row.columns.array.find(column => column.name === "berechnen");
		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch col-12 col-sm-4 pb-3",
				id: column.name,
				text: column.title,
				status: (row.cells && row.cells[column.name]) ?? true,
			})
		);

		modalBody.appendChild(document.createElement("hr"));

		const titleWrapper = document.createElement("div");
		titleWrapper.className = "icon-ewt";
		modalBody.appendChild(titleWrapper);

		titleWrapper.appendChild(createModalBodySpanElement("material-icons-round big-icons", "arrow_downward"));
		titleWrapper.appendChild(createModalBodyTitelElement("Wohnung"));
		titleWrapper.appendChild(createModalBodySpanElement("material-icons-round big-icons", "arrow_upward"));

		column = row.columns.array.find(column => column.name === "abWE");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || "",
				type: "time",
			})
		);

		column = row.columns.array.find(column => column.name === "anWE");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || "",
				type: "time",
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Arbeitszeit"));

		column = row.columns.array.find(column => column.name === "beginE");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || "",
				type: "time",
			})
		);

		column = row.columns.array.find(column => column.name === "endeE");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || "",
				type: "time",
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("1. Tätigkeitsstätte"));

		column = row.columns.array.find(column => column.name === "ab1E");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || "",
				type: "time",
			})
		);

		column = row.columns.array.find(column => column.name === "an1E");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || "",
				type: "time",
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Einsatzort"));

		column = row.columns.array.find(column => column.name === "anEE");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || "",
				type: "time",
			})
		);

		column = row.columns.array.find(column => column.name === "abEE");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || "",
				type: "time",
			})
		);

		return modalBody;
	}

	function customFooterButtton() {
		if (row instanceof Row) {
			const btnClearZeiten = document.createElement("button");
			btnClearZeiten.type = "button";
			btnClearZeiten.className = "btn btn-danger";
			btnClearZeiten.arialabel = "Zeiten löschen";
			btnClearZeiten.innerHTML = "Zeiten löschen";
			btnClearZeiten.addEventListener("click", () => clearZeiten(modal));
			return [btnClearZeiten];
		}
	}

	function SubmitEventListener() {
		return e => {
			if (form.checkValidity && !form.checkValidity()) return;
			e.preventDefault();

			const row = modal.row,
				values = {
					tagE: dayjs(form.querySelector("#tagE").value).format("DD"),
					eOrtE: form.querySelector("#eOrtE").value,
					schichtE: form.querySelector("#schichtE").value,
					abWE: form.querySelector("#abWE").value,
					ab1E: form.querySelector("#ab1E").value,
					anEE: form.querySelector("#anEE").value,
					beginE: form.querySelector("#beginE").value,
					endeE: form.querySelector("#endeE").value,
					abEE: form.querySelector("#abEE").value,
					an1E: form.querySelector("#an1E").value,
					anWE: form.querySelector("#anWE").value,
					berechnen: form.querySelector("#berechnen").checked,
				};

			row instanceof Row ? row.val(values) : row.rows.add(values);

			Modal.getInstance(modal).hide();
			saveTableData("tableE", row.CustomTable || row);
		};
	}
}
function createShowModalEWT(row, title) {
	const { modal } = createModal(title, false, "sm", createModalBody, createShowModalFooter);
	return modal;

	function createModalBody() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";
		let column;

		column = row.columns.array.find(column => column.name === "tagE");
		modalBody.appendChild(
			createModalBodyShowElement({
				divClass: "mb-1 col-6",
				labelClass: "col-5 col-form-label text-wrap fw-bold",
				spanClass: "col-2 align-middle text-break my-auto",
				title: `${column.title}:`,
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		column = row.columns.array.find(column => column.name === "berechnen");
		const berechnenCheckbox = createModalBodyCheckbox({
			checkClass: "form-check form-switch col-5",
			id: column.name,
			text: column.title,
			status: (row.cells && row.cells[column.name]) ?? true,
		});
		berechnenCheckbox.addEventListener("click", e => {
			const row = e.target.closest(".modal").row;
			row.cells.berechnen = e.target.checked;
			const table = row.CustomTable;
			table.drawRows();
			saveTableData("tableE", table);
		});
		modalBody.appendChild(berechnenCheckbox);

		column = row.columns.array.find(column => column.name === "eOrtE");
		modalBody.appendChild(
			createModalBodyShowElement({
				labelClass: "col-4 col-sm-5 col-form-label text-wrap fw-bold",
				spanClass: "col-8 col-sm-7 align-middle text-break my-auto",
				title: `${column.title}:`,
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		column = row.columns.array.find(column => column.name === "schichtE");
		modalBody.appendChild(
			createModalBodyShowElement({
				labelClass: "col-4 col-sm-5 col-form-label text-wrap fw-bold",
				spanClass: "col-8 col-sm-7 align-middle text-break my-auto",
				title: `${column.title}:`,
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		modalBody.appendChild(document.createElement("hr"));

		const titleWrapper = document.createElement("div");
		titleWrapper.className = "icon-ewt-arrow";
		modalBody.appendChild(titleWrapper);

		titleWrapper.appendChild(createModalBodySpanElement("material-icons-round big-icons", "arrow_downward"));
		titleWrapper.appendChild(createModalBodySpanElement("material-icons-round big-icons", "arrow_upward"));

		modalBody.appendChild(
			createModalTitleElement({
				vorTitle: "ab",
				title: "Wohnung",
				nachTitle: "an",
			})
		);

		column = row.columns.array.find(column => column.name === "abWE");
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);
		column = row.columns.array.find(column => column.name === "anWE");
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		modalBody.appendChild(
			createModalTitleElement({
				vorTitle: "von",
				title: "Arbeitszeit",
				nachTitle: "bis",
			})
		);

		column = row.columns.array.find(column => column.name === "beginE");
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);
		column = row.columns.array.find(column => column.name === "endeE");
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		modalBody.appendChild(
			createModalTitleElement({
				vorTitle: "ab",
				title: "1. Tätigkeitsstätte",
				nachTitle: "an",
			})
		);

		column = row.columns.array.find(column => column.name === "ab1E");
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);
		column = row.columns.array.find(column => column.name === "an1E");
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		modalBody.appendChild(
			createModalTitleElement({
				vorTitle: "an",
				title: "Einsatzort",
				nachTitle: "ab",
			})
		);

		column = row.columns.array.find(column => column.name === "anEE");
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);
		column = row.columns.array.find(column => column.name === "abEE");
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		return modalBody;

		function createModalTitleElement(options) {
			const titleWrapper = document.createElement("div");
			titleWrapper.className = options.divClass ?? "icon-ewt";

			const title = document.createElement("h5");
			title.className = "my-1";
			title.textContent = options.title;

			titleWrapper.appendChild(createModalBodySpanElement("", options.vorTitle));
			titleWrapper.appendChild(title);
			titleWrapper.appendChild(createModalBodySpanElement("", options.nachTitle));

			return titleWrapper;
		}

		function createModalBodyShowElement2(options) {
			const divElement = document.createElement("div");
			divElement.className = options.divClass ?? "mb-1 col-6 text-center";

			const span = document.createElement("span");
			span.className = options.spanClass ?? "";
			span.id = options.name;
			span.innerHTML = options.text; //|| "--:--";
			divElement.appendChild(span);

			return divElement;
		}
	}
}

export function DataE(data) {
	if (data == null) {
		if (localStorage.getItem("dataE") == null) return [];
		data = JSON.parse(localStorage.getItem("dataE"));
	}

	if (data.length === 0 || isObject(data[0])) return data;

	let data1 = [];
	data.forEach((row, i) => {
		data1[i] = {
			tagE: row[0],
			eOrtE: row[1],
			schichtE: row[2] === "" ? "T" : row[2],
			abWE: row[3],
			ab1E: row[4],
			anEE: row[5],
			beginE: row[6],
			endeE: row[7],
			abEE: row[8],
			an1E: row[9],
			anWE: row[10],
			berechnen: typeof row[11] === "string" ? JSON.parse(row[11]) : row[11] || true,
		};
	});
	return data1;
}

function clearZeiten(modal) {
	modal.querySelector("#abWE").value = "";
	modal.querySelector("#ab1E").value = "";
	modal.querySelector("#anEE").value = "";
	modal.querySelector("#beginE").value = "";
	modal.querySelector("#endeE").value = "";
	modal.querySelector("#abEE").value = "";
	modal.querySelector("#an1E").value = "";
	modal.querySelector("#anWE").value = "";
}

function ewtBerechnen(monat, jahr, daten, vorgabenU) {
	monat = Number(monat);
	jahr = Number(jahr);
	daten = JSON.parse(daten);
	vorgabenU = JSON.parse(vorgabenU);
	daten = convertDaten(daten, jahr, monat);

	DatenSortieren(daten);

	daten = berechnen(vorgabenU, daten, jahr, monat);

	for (const zeile of daten) {
		if (!zeile.berechnen) continue;
		zeile.tagE = `0${zeile.tagE}`.slice(-2);
		for (const [key, value] of Object.entries(zeile)) {
			switch (key) {
				case "tagE":
				case "schichtE":
				case "eOrtE":
				case "berechnen":
					break;
				case "abWE":
				case "ab1E":
				case "anEE":
				case "beginE":
				case "endeE":
				case "abEE":
				case "an1E":
				case "anWE":
					if (value) zeile[key] = value.format("LT");
					break;
				default:
					throw Error("Datenfehler");
			}
		}
	}

	const ftE = document.querySelector("#tableE").instance;
	console.log("save ", { ftE });
	ftE.rows.load(DataE(daten));

	saveTableData("tableE", ftE);
	createSnackBar({
		message: `EWT<br/>Zeiten berechnet.`,
		status: "success",
		timeout: 3000,
		position: "br",
		fixed: true,
	});

	function convertDaten(daten, jahr, monat) {
		for (const zeile of daten) {
			if (!zeile.berechnen) continue;
			const tag = Number(zeile.tagE);
			zeile.tagE = tag;

			for (const [key, value] of Object.entries(zeile)) {
				switch (key) {
					case "tagE":
					case "schichtE":
					case "eOrtE":
					case "berechnen":
						break;
					case "abWE":
					case "ab1E":
					case "anEE":
					case "beginE":
					case "endeE":
					case "abEE":
					case "an1E":
					case "anWE":
						if (value) {
							let tagI = tag;
							let zeit = value.split(":");
							if (["endeE", "abEE", "an1E", "anWE"].includes(key) && ["BN", "N"].includes(zeile.schichtE)) {
								tagI = zeile.tagE - 1;
							}
							zeile[key] = dayjs([jahr, monat - 1, tagI, zeit[0], zeit[1], 0, 0]);
						} else {
							zeile[key] = "";
						}
						break;
					default:
						throw Error("Datenfehler");
				}
			}
		}
		return daten;
	}
}

function berechnen(vorgabenU, daten, jahr, monat) {
	let beginn, ende, svzA, svzE, datumAb1, datumBeginn, datumEnde, datumAn1;

	const endePascal = vorgabenU.pers.Name == "Ackermann, Pascal" ? dayjs.duration(5, "m") : dayjs.duration(0, "m");

	const getTimeForDuration = value => {
		const time = dayjs(value, "HH:mm");
		return { hours: time.hour(), minutes: time.minute() };
	};

	const vorgabenE = vorgabenU.aZ;
	for (const [key, value] of Object.entries(vorgabenE)) vorgabenE[key] = dayjs.duration(getTimeForDuration(value));

	vorgabenE.fZ = {};

	vorgabenU.fZ.map(ort => {
		vorgabenE.fZ[ort.key] = dayjs.duration(getTimeForDuration(ort.value));
	});

	// Beginn der Berechnung
	for (const Tag of daten) {
		if (!Tag.berechnen) continue;
		let datum = dayjs([jahr, monat - 1, Tag.tagE]);
		switch (Tag.schichtE) {
			case "N":
				beginn = vorgabenE.bN;
				ende = vorgabenE.eN.add(1, "d");
				svzA = dayjs.duration(45, "m");
				svzE = svzA.clone();
				break;
			case "S":
				beginn = vorgabenE.bS;
				ende = vorgabenE.eS;
				svzA = dayjs.duration(20, "m");
				svzE = svzA.clone();
				break;
			case "BN":
				beginn = vorgabenE.bBN;
				ende = vorgabenE.eN.add(1, "d");
				svzA = dayjs.duration(60, "m");
				svzE = dayjs.duration(45, "m");
				break;
			case "T":
				beginn = vorgabenE.bT;
				ende = datum.isoWeekday() === 5 ? vorgabenE.eTF : vorgabenE.eT;
				svzA = dayjs.duration(20, "m");
				svzE = svzA.clone();
				break;
			default:
				throw Error("Schichtfehler");
		}

		// Arbeitszeit Beginn
		Tag.beginE = datumBeginn = Tag.beginE === "" ? datum.add(beginn) : Tag.beginE;
		// Arbeitszeit Ende
		Tag.endeE = datumEnde = Tag.endeE === "" ? datum.add(ende) : Tag.endeE;
		// Ab Wohnung
		Tag.abWE = Tag.abWE === "" ? datumBeginn.subtract(vorgabenE.rZ) : Tag.abWE;
		// Ab erste Tätigkeitsstätte
		Tag.ab1E = datumAb1 = Tag.ab1E === "" ? datumBeginn.add(svzA) : Tag.ab1E;
		// An erste Tätigkeitsstätte
		Tag.an1E = datumAn1 = Tag.an1E === "" ? datumEnde.subtract(svzE) : Tag.an1E;
		// An Wohnung
		Tag.anWE = Tag.anWE === "" ? datumEnde.add(vorgabenE.rZ).add(endePascal) : Tag.anWE;
		if (Tag.eOrtE !== "") {
			// An Einsatzort
			Tag.anEE = Tag.anEE === "" ? datumAb1.add(vorgabenE.fZ[Tag.eOrtE]) : Tag.anEE;
			// Ab Einsatzort
			Tag.abEE = Tag.abEE === "" ? datumAn1.subtract(vorgabenE.fZ[Tag.eOrtE]) : Tag.abEE;
		}
	}
	return daten;
}

function naechsterTag(tag, dataE = tableToArray("tableE")) {
	const eingabefeldTagE = document.getElementById("tagE");
	if (tag == null) tag = dayjs(eingabefeldTagE.value).date();
	if (tag == "") tag = 0;
	if (typeof tag === "string") tag = +tag;
	let vorhandeTage = new Set(dataE.map(tag => +tag.tagE)),
		letzterTag = dayjs(document.getElementById("tagE").max).date(),
		loop = false;
	vorhandeTage.delete(0);

	do {
		tag++;
		if (tag > letzterTag && !loop) {
			tag = 1;
			loop = true;
		} else if (tag > letzterTag && loop) throw Error("Fehler beim Finden eines Freien Tages");
	} while (vorhandeTage.has(tag));

	eingabefeldTagE.value = dayjs([+localStorage.getItem("Jahr"), +localStorage.getItem("Monat") - 1, tag]).format(
		"YYYY-MM-DD"
	);
}

function addEWTtag() {
	let tagE = `0${document.getElementById("tagE").value}`.slice(-2),
		eOrtE = document.getElementById("EOrt").value,
		schichtE = document.getElementById("Schicht").value,
		berechnen = document.getElementById("berechnen1").checked;

	let data = {
		tagE,
		eOrtE,
		schichtE,
		abWE: "",
		ab1E: "",
		anEE: "",
		beginE: "",
		endeE: "",
		abEE: "",
		an1E: "",
		anWE: "",
		berechnen,
	};
	const ftE = document.querySelector("#tableE").instance;
	console.log("save ", { ftE });
	ftE.rows.add(data);

	saveTableData("tableE", ftE);
	naechsterTag(
		+tagE,
		ftE.getRows().map(row => row.cells)
	);
}
