import dayjs from "dayjs";
import arraySupport from "dayjs/plugin/arraySupport";
import { generateTableBerechnung } from "./Berechnung.js";
import { DataBE, DataBZ } from "./Bereitschaft.js";
import {
	buttonDisable,
	clearLoading,
	controller,
	FetchRetry,
	newAbortControler,
	setDisableButton,
	setLoading,
} from "./Core.js";
import { generateEingabeMaskeEinstellungen } from "./Einstellungen.js";
import { DataE } from "./EWT.js";
import { createSnackBar } from "./js-snackbar.js";
import { DataN } from "./Neben.js";
dayjs.extend(arraySupport);

window.addEventListener("load", () => {
	if (localStorage.length > 3 && localStorage.getItem("UserID")) {
		const benutzer = localStorage.getItem("Benutzer");
		Logout();
		createSnackBar({
			message: `Hallo ${benutzer},<br/>die App hat ein Update erhalten.<br/>Bitte melde dich neu an, um<br/>die neuen Funktionen zu nutzen.`,
			status: "info",
			timeout: 10000,
			position: "br",
			fixed: true,
		});
	}

	document.getElementById("formLogin").addEventListener("submit", e => {
		e.preventDefault();
		loginUser();
	});
	document.getElementById("formNewUser").addEventListener("submit", e => {
		e.preventDefault();
		checkNeuerBenutzer();
	});
	document.getElementById("formChangePW").addEventListener("submit", e => {
		e.preventDefault();
		checkPasswort();
	});
	document.getElementById("formSelectMonatJahr").addEventListener("submit", e => {
		e.preventDefault();
		SelectYear();
	});
	document.getElementById("btnNewUser").addEventListener("click", () => {
		document.getElementById("loginDisplay").classList.add("d-none");
		document.getElementById("NewDisplay").classList.remove("d-none");
	});
	document.getElementById("btnCancelNewUser").addEventListener("click", () => {
		document.getElementById("NewDisplay").classList.add("d-none");
		document.getElementById("loginDisplay").classList.remove("d-none");
	});
	document.getElementById("btnCancelChangePW").addEventListener("click", () => {
		document.getElementById("ChangeDisplay").classList.add("d-none");
		document.getElementById("SelectDisplay").classList.remove("d-none");
	});
	document.getElementById("btnPasswortAEndern").addEventListener("click", () => {
		document.getElementById("SelectDisplay").classList.add("d-none");
		document.getElementById("ChangeDisplay").classList.remove("d-none");
	});
	document.getElementById("btnLogout").addEventListener("click", Logout);
	document.getElementById("Monat").addEventListener("change", changeMonatJahr);
	document.getElementById("Jahr").addEventListener("change", changeMonatJahr);

	if (localStorage.getItem("Benutzer") && localStorage.getItem("accessToken")) {
		let benutzer = localStorage.getItem("Benutzer");
		document.getElementById("Willkommen").innerHTML = `Hallo, ${benutzer}.`;
		document.getElementById("loginDisplay").classList.add("d-none");

		let jahr = localStorage.getItem("Jahr");
		document.getElementById("Jahr").value = jahr;

		let monat = localStorage.getItem("Monat");
		document.getElementById("Monat").value = monat;

		document.getElementById("SelectDisplay").classList.remove("d-none");
		console.log("Benutzer gefunden");

		if (JSON.parse(localStorage.getItem("VorgabenU"))?.pers.TB == "Tarifkraft")
			document.getElementById("Neben-tab").parentElement.classList.remove("d-none");
		if (benutzer === "Jan") document.getElementById("admin").classList.remove("d-none");

		document.getElementById("navmenu").classList.remove("d-none");
		document.getElementById("btn-navmenu").classList.remove("d-none");

		if (navigator.onLine) SelectYear(monat, jahr);
	}
});

/**
 *
 * @returns {void}
 */
export function changeMonatJahr() {
	if (
		document.getElementById("Monat").value === localStorage.getItem("Monat") &&
		document.getElementById("Jahr").value === localStorage.getItem("Jahr")
	) {
		buttonDisable(false);
		if (!navigator.onLine) setDisableButton(true);
		return;
	}
	buttonDisable(true);
	if (navigator.onLine) document.querySelector("#btnAuswaehlen").disabled = false;
}

/**
 *
 * @param {string} [username]
 * @param {string} [passwort]
 */
async function loginUser(
	username = document.getElementById("Benutzer").value,
	passwort = document.getElementById("Passwort").value
) {
	document.getElementById("btnLogin").disabled = true;
	setLoading("btnLogin");

	let data = { Name: username, Passwort: passwort };

	try {
		const fetched = await FetchRetry("login", data, "POST");
		if (fetched.statusCode == 200) {
			let { accessToken, refreshToken } = fetched.data;
			console.log({ accessToken, refreshToken });
			username = `${username[0].toUpperCase()}${username.substring(1)}`;
			localStorage.setItem("Benutzer", username);
			document.getElementById("Willkommen").innerHTML = `Hallo, ${username}.`;
			document.getElementById("loginDisplay").classList.add("d-none");
			document.getElementById("ChangeDisplay").classList.add("d-none");
			document.getElementById("NewDisplay").classList.add("d-none");

			localStorage.setItem("accessToken", accessToken);
			localStorage.setItem("refreshToken", refreshToken);

			const aktJahr = dayjs().year();
			document.getElementById("Jahr").value = aktJahr;

			const monat = dayjs().month() + 1;
			document.getElementById("Monat").value = monat;

			document.getElementById("SelectDisplay").classList.remove("d-none");
			document.getElementById("Benutzer").value = "";
			document.getElementById("Passwort").value = "";
			document.getElementById("errorMessage").innerHTML = "";

			if (username === "Jan") document.getElementById("admin").classList.remove("d-none");

			console.log("Eingeloggt");
			SelectYear(monat, aktJahr);
		} else {
			document.getElementById("errorMessage").innerHTML = fetched.message;
		}
	} catch (err) {
		console.log(err.message);
	} finally {
		clearLoading("btnLogin");
	}
}

/**
 *
 * @param {number} [monat]
 * @param {number} [jahr]
 * @returns {void}
 */
function SelectYear(monat = document.getElementById("Monat").value, jahr = document.getElementById("Jahr").value) {
	setLoading("btnAuswaehlen");
	document.getElementById("errorMessage").innerHTML = "";

	if (localStorage.getItem("Jahr") && localStorage.getItem("Monat")) {
		if (localStorage.getItem("Jahr") != jahr || localStorage.getItem("Monat") != monat) {
			localStorage.setItem("monatswechsel", true);
		}
	}
	localStorage.setItem("Jahr", jahr);
	localStorage.setItem("Monat", monat);

	setMonatJahr(jahr, monat);

	if (!localStorage.getItem("accessToken")) return;

	LadeUserDaten(monat, jahr);
}

export function Logout() {
	controller.abort();
	newAbortControler();
	localStorage.clear();
	document.getElementById("navmenu").classList.add("d-none");
	document.getElementById("btn-navmenu").classList.add("d-none");
	document.getElementById("loginDisplay").classList.remove("d-none");
	document.getElementById("NewDisplay").classList.add("d-none");
	document.getElementById("SelectDisplay").classList.add("d-none");
	document.getElementById("admin").classList.add("d-none");
	document.getElementById("Neben-tab").parentElement.classList.add("d-none");
	document.getElementById("Willkommen").innerHTML = "Willkommen";
}

async function checkPasswort() {
	const passwortAlt = document.getElementById("passwortOld").value,
		passwort3 = document.getElementById("Passwort3").value,
		passwort4 = document.getElementById("Passwort4").value;

	if (!passwortAlt) {
		document.getElementById("errorMessage").innerHTML = "Bitte Aktuelles Passwort Eingeben";
		return;
	}
	if (!passwort3) {
		document.getElementById("errorMessage").innerHTML = "Bitte Neues Passwort Eingeben";
		return;
	}
	if (!passwort4) {
		document.getElementById("errorMessage").innerHTML = "Bitte Neues Passwort wiederholen";
		return;
	}
	if (passwort3 != passwort4) {
		document.getElementById("errorMessage").innerHTML = "Passworter falsch wiederholt";
		return;
	}
	if (passwortAlt == passwort3) {
		document.getElementById("errorMessage").innerHTML = "Passwörter Alt und Neu sind gleich";
		return;
	}

	setLoading("btnChange");

	try {
		let data = {
			PasswortAlt: passwortAlt,
			PasswortNeu: passwort3,
		};

		const fetched = await FetchRetry("changePW", data, "POST");
		if (fetched.statusCode >= 400) {
			console.log(fetched.message);
			document.getElementById("errorMessage").innerHTML = fetched.message;
			createSnackBar({
				message: `Login<br/>Passwort konnte nicht geändert werden.`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		} else if (fetched.statusCode == 200) {
			console.log(`Passwort geändert: ${fetched.data}`);
			createSnackBar({
				message: `Login<br/>Passwort wurde geändert.`,
				status: "success",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			document.getElementById("errorMessage").innerHTML = "";
		} else {
			console.log("Fehler");
			createSnackBar({
				message: `Login<br/>Passwort konnte nicht geändert werden.`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}
		document.getElementById("SelectDisplay").classList.remove("d-none");
		document.getElementById("ChangeDisplay").classList.add("d-none");
		document.getElementById("passwortOld").value = "";
		document.getElementById("Passwort3").value = "";
		document.getElementById("Passwort4").value = "";
	} catch (err) {
		console.log(err);
		return;
	} finally {
		clearLoading("btnChange");
	}
}

async function checkNeuerBenutzer() {
	const zugangscode = document.getElementById("Zugang").value,
		benutzer = document.getElementById("Benutzer2").value,
		passwort1 = document.getElementById("Passwort1").value,
		passwort2 = document.getElementById("Passwort2").value;

	if (!zugangscode) {
		document.getElementById("errorMessage").innerHTML = "Bitte Zugangscode Eingeben";
		return;
	}
	if (!benutzer) {
		document.getElementById("errorMessage").innerHTML = "Bitte Benutzername Eingeben";
		return;
	}
	if (!passwort1) {
		document.getElementById("errorMessage").innerHTML = "Bitte Passwort Eingeben";
		return;
	}
	if (!passwort2) {
		document.getElementById("errorMessage").innerHTML = "Bitte Passwort wiederholen";
		return;
	}
	if (passwort1 != passwort2) {
		document.getElementById("errorMessage").innerHTML = "Passwörter falsch wiederholt";
		return;
	}
	setLoading("btnNeu");
	let data = {
		Code: zugangscode,
		Name: benutzer,
		Passwort: passwort1,
	};
	try {
		const fetched = await FetchRetry("add", data, "POST");
		if (fetched.statusCode >= 400) {
			console.log(fetched.message);
			document.getElementById("errorMessage").innerHTML = fetched.message;
			return;
		}
		if (fetched.statusCode == 201) {
			createSnackBar({
				message: `Registrierung<br/>Benutzer erfolgreich angelegt.`,
				status: "success",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
		} else {
			console.log("Fehler: ", fetched.message);
			createSnackBar({
				message: `Registrierung <br/>Fehler bei Benutzerstellung: </br>${fetched.message}`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}
	} catch (err) {
		console.log(err);
		return;
	} finally {
		clearLoading("btnNeu");
	}

	document.getElementById("errorMessage").innerHTML = "";

	loginUser(document.getElementById("Benutzer2").value, document.getElementById("Passwort1").value);

	document.getElementById("Zugang").value = "";
	document.getElementById("Benutzer2").value = "";
	document.getElementById("Passwort1").value = "";
	document.getElementById("Passwort2").value = "";
}

/**
 *
 * @param {number} monat
 * @param {number} jahr
 * @param {string} accessToken
 * @returns
 */
async function LadeUserDaten(monat, jahr) {
	setMonatJahr(jahr, monat);
	let user;
	try {
		const fetched = await FetchRetry(`${monat}&${jahr}`);
		if (fetched.statusCode == 200) {
			user = fetched.data;
		} else {
			throw Error(fetched.message);
		}
	} catch (err) {
		console.error(err);
		createSnackBar({
			message: `Server <br/>Keine Verbindung zum Server oder Serverfehler.`,
			status: "error",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
		return;
	} finally {
		clearLoading("btnAuswaehlen");
	}

	const dataArray = user;

	console.log("Daten geladen: ", dataArray);
	let { vorgabenB, vorgabenU, datenBerechnung, datenGeld } = dataArray,
		dataBZ = dataArray.datenB.datenBZ,
		dataBE = dataArray.datenB.datenBE,
		dataE = dataArray.datenE,
		dataN = dataArray.datenN;

	let vorhanden = [];

	let monatswechsel = false;

	if (localStorage.getItem("monatswechsel")) {
		monatswechsel = true;
		localStorage.removeItem("monatswechsel");
	}

	let dataServer = {};
	if (localStorage.getItem("dataServer")) {
		dataServer = JSON.parse(localStorage.getItem("dataServer"));
		console.log("Unterschiede Server - Client | Bereits vorhanden", dataServer);
	}

	localStorage.setItem("VorgabenB", JSON.stringify(vorgabenB));

	const saveDaten = (nameStorage, name, data, dataName, beschreibung, loadTable = false) => {
		if (!localStorage.getItem(nameStorage)) {
			console.log(`${name} speichern, nicht vorhanden`);
			localStorage.setItem(nameStorage, JSON.stringify(data));
			if (loadTable) document.getElementById(loadTable.name).instance.rows.load(loadTable.data);
		} else if (monatswechsel) {
			console.log(`${name} speichern`);
			localStorage.setItem(nameStorage, JSON.stringify(data));
		} else if (localStorage.getItem(nameStorage) !== JSON.stringify(data)) {
			console.log(`${name} vorhanden & änderungen`);
			if (!vorhanden.find(element => element == beschreibung)) vorhanden.push(beschreibung);
			dataServer[dataName] = data;
			return JSON.parse(localStorage.getItem(nameStorage));
		}
		return data;
	};
	vorgabenU = saveDaten("VorgabenU", "VorgabenU", vorgabenU, "vorgabenU", "Persönliche Daten");
	dataBZ = saveDaten("dataBZ", "DatenBZ", dataBZ, "dataBZ", "Bereitschaftszeit", {
		name: "tableBZ",
		data: DataBZ(dataBZ),
	});
	dataBE = saveDaten("dataBE", "DatenBE", dataBE, "dataBE", "Bereitschaftseinsatz", {
		name: "tableBE",
		data: DataBE(dataBE),
	});
	dataE = saveDaten("dataE", "DatenE", dataE, "dataE", "EWT", {
		name: "tableE",
		data: DataE(dataE),
	});
	dataN = saveDaten("dataN", "DatenN", dataN, "dataN", "Nebenbezüge", {
		name: "tableN",
		data: DataN(dataN),
	});

	localStorage.setItem("datenBerechnung", JSON.stringify(datenBerechnung));
	localStorage.setItem("VorgabenGeld", JSON.stringify(datenGeld));

	if (vorhanden.length > 0) {
		if (Object.keys(dataServer).length > 0) console.log("Unterschiede Server - Client", dataServer);
		localStorage.setItem("dataServer", JSON.stringify(dataServer));

		createSnackBar({
			message: `<b>Ungespeicherte Daten:</b> <br/>${vorhanden.join("<br/>")}`,
			status: "info",
			dismissible: false,
			timeout: false,
			position: "br",
			fixed: true,
			actions: [
				{
					text: "Lokale Daten überschreiben",
					function: () => {
						SaveUserDatenUEberschreiben();
						clearLoading("btnAuswaehlen");
						buttonDisable(false);
					},
					dismiss: true,
					class: ["text-primary"],
				},
				{
					text: "Daten behalten",
					function: () => {
						SaveUserDatenUEberschreiben(false);
						clearLoading("btnAuswaehlen");
						buttonDisable(false);
					},
					dismiss: true,
					class: ["text-secondary"],
				},
			],
		});
	} else {
		clearLoading("btnAuswaehlen");
		buttonDisable(false);
	}

	if (dataServer.dataBZ != null || monatswechsel) document.getElementById("tableBZ").instance.rows.load(DataBZ(dataBZ));
	if (dataServer.dataBE != null || monatswechsel) document.getElementById("tableBE").instance.rows.load(DataBE(dataBE));
	if (dataServer.dataE != null || monatswechsel) document.getElementById("tableE").instance.rows.load(DataE(dataE));
	if (dataServer.dataN != null || monatswechsel) document.getElementById("tableN").instance.rows.load(DataN(dataN));

	generateTableBerechnung(datenBerechnung, datenGeld);
	generateEingabeMaskeEinstellungen(vorgabenU);

	if (vorgabenU.pers.TB == "Tarifkraft") document.getElementById("Neben-tab").parentElement.classList.remove("d-none");
	document.getElementById("navmenu").classList.remove("d-none");
	document.getElementById("btn-navmenu").classList.remove("d-none");
	createSnackBar({
		message: `Neue Daten geladen.`,
		status: "success",
		timeout: 3000,
		position: "br",
		fixed: true,
	});
}

/**
 *
 * @param {number} jahr
 * @param {number} monat
 */
function setMonatJahr(jahr, monat) {
	document.getElementById("Monat").value = monat;
	let datum = dayjs([+jahr, monat - 1]);
	document.getElementById("MonatB").innerText =
		document.getElementById("MonatE").innerText =
		document.getElementById("MonatN").innerText =
			datum.format("MM / YY");
	document.getElementById("MonatBerechnung").innerText = jahr;
}

/**
 *
 * @param {boolean} ueberschreiben
 * @returns
 */
function SaveUserDatenUEberschreiben(ueberschreiben = true) {
	if (!ueberschreiben) {
		localStorage.removeItem("dataServer");
		return;
	}
	let dataServer = JSON.parse(localStorage.getItem("dataServer"));
	console.log({ dataServer });
	if (dataServer.vorgabenU) {
		console.log("VorgabenU überschreiben");
		localStorage.setItem("VorgabenU", JSON.stringify(dataServer.vorgabenU));
		generateEingabeMaskeEinstellungen(dataServer.vorgabenU);
		delete dataServer.vorgabenU;
	}
	if (dataServer.dataBZ) {
		console.log("DatenBZ überschreiben");
		localStorage.setItem("dataBZ", JSON.stringify(dataServer.dataBZ));
		document.getElementById("tableBZ").instance.rows.load(DataBZ(dataServer.dataBZ));
		delete dataServer.dataBZ;
	}
	if (dataServer.dataBE) {
		console.log("DatenBE überschreiben");
		localStorage.setItem("dataBE", JSON.stringify(dataServer.dataBE));
		document.getElementById("tableBE").instance.rows.load(DataBE(dataServer.dataBE));
		delete dataServer.dataBE;
	}
	if (dataServer.dataE) {
		console.log("DatenE überschreiben");
		localStorage.setItem("dataE", JSON.stringify(dataServer.dataE));
		document.getElementById("tableE").instance.rows.load(DataE(dataServer.dataE));
		delete dataServer.dataE;
	}
	if (dataServer.dataN) {
		console.log("DatenN überschreiben");
		localStorage.setItem("dataN", JSON.stringify(dataServer.dataN));
		document.getElementById("tableN").instance.rows.load(DataN(dataServer.dataN));
		delete dataServer.dataN;
	}

	localStorage.removeItem("dataServer");
}
