import { Modal } from "bootstrap";
import dayjs from "dayjs";
import "dayjs/locale/de";
import arraySupport from "dayjs/plugin/arraySupport";
import customParseFormat from "dayjs/plugin/customParseFormat";
import LocalizedFormat from "dayjs/plugin/LocalizedFormat";
import {
	buttonDisable,
	createEditorModalFooter,
	createModal,
	createModalBodyInputElement,
	createModalBodySelectElement,
	createModalBodyShowElement,
	createModalBodySpanElement,
	createModalBodyTitelElement,
	createShowModalFooter,
	download,
	isObject,
	saveDaten,
	saveTableData,
	tableToArray,
} from "./Core.js";
import { createCustomTable, CustomTable, Row } from "./CustomTable";
import { createSnackBar } from "./js-snackbar.js";
dayjs.extend(arraySupport);
dayjs.extend(customParseFormat);
dayjs.extend(LocalizedFormat);

window.addEventListener("load", () => {
	const ftN = createCustomTable("tableN", {
		columns: [
			{ name: "tagN", title: "Tag", sortable: true, sorted: true, direction: "ASC" },
			{ name: "beginN", title: "Arbeit Von", type: "time" },
			{ name: "endeN", title: "Arbeit Bis", type: "time" },
			{ name: "beginPauseN", title: "Pause Von", breakpoints: "sm", type: "time" },
			{ name: "endePauseN", title: "Pause Bis", breakpoints: "sm", type: "time" },
			{ name: "dauerN", title: "Anzahl", breakpoints: "md" },
			{ name: "nrN", title: "Zulage", breakpoints: "md" },
		],
		rows: DataN(),
		sorting: { enabled: true },
		editing: {
			enabled: true,
			addRow: () => {
				const $modal = createEditorModalNeben(ftN, "Nebenbezug hinzufügen");
				$modal.row = ftN;
				new Modal($modal).show();
			},
			editRow: row => {
				const $modal = createEditorModalNeben(row, "Nebenbezug bearbeiten");
				$modal.row = row;
				new Modal($modal).show();
			},
			showRow: row => {
				const $modal = createShowModalNeben(row, "Nebenbezug anzeigen");
				$modal.row = row;
				new Modal($modal).show();
			},
			deleteRow: row => {
				row.deleteRow();
				saveTableData("tableN", ftN);
			},
			deleteAllRows: () => {
				if (confirm("Möchtest du wirklich alle Zeilen löschen?")) {
					ftN.rows.load([]);
					buttonDisable(false);
					localStorage.setItem("dataBE", JSON.stringify([]));
				}
			},
		},
	});

	document.querySelector("#btnESN").addEventListener("click", () => {
		createAddModalNeben();
	});
	document.querySelector("#btnSaveN").addEventListener("click", () => {
		saveDaten(document.querySelector("#btnSaveN"));
	});
	document.querySelector("#btnDownloadN").addEventListener("click", () => {
		download(document.querySelector("#btnDownloadN"), "N");
	});
});

function createAddModalNeben() {
	const { modal, form } = createModal(
		"Neuen Nebenbezug eingeben",
		true,
		null,
		createBodyElement,
		createEditorModalFooter([customFooterButtton()]),
		SubmitEventListener
	);
	new Modal(modal).show();

	function createBodyElement() {
		const dataE = tableToArray("tableE");
		if (dataE.length === 0) {
			createSnackBar({
				message: 'Keine Tage in EWT gefunden. </br></br>Bitte erst EWT ausfüllen! </br>oder Manuell über "Neue Zeile"',
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}
		console.log(dataE);

		const modalBody = document.createElement("div");
		modalBody.className = "modal-body";

		const warnung = document.createElement("p");
		warnung.className = "text-center";
		warnung.textContent = "!!! Erst EWT Eingeben und Berechnen !!!";
		modalBody.appendChild(warnung);

		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col mb-3",
				title: "Tag (Aus EWT)",
				name: "tagN",
				required: true,
				value: null,
				options: getTagOptions(),
			})
		);

		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col mb-3",
				title: "Nebenbezug (Aktuell nur 1 möglichkeit)",
				name: "Nebenbezug",
				required: true,
				value: null,
				options: [
					{
						value: "040 Fahrentsch.",
						text: "040 Fahrentsch.",
						selected: true,
					},
				],
			})
		);

		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col",
				title: "Anzahl",
				name: "AnzahlN",
				value: 1,
				type: "number",
				required: true,
				min: 1,
				max: 1,
			})
		);

		return modalBody;

		function getTagOptions() {
			const dataN = tableToArray("tableN");
			const jahr = +localStorage.getItem("Jahr");
			const monat = +localStorage.getItem("Monat");
			let options = [];
			for (const day of dataE) {
				const schicht = day.schichtE;
				let tagN,
					tag,
					pauseA = "12:00",
					pauseE = "12:30";
				if (["N", "BN"].includes(schicht)) {
					tagN = `0${day.tagE - 1}`.slice(-2);
					tag = `${tagN} | ${new Date(jahr, monat - 1, day.tagE - 1).toLocaleDateString("de", {
						weekday: "short",
					})}`;
					pauseA = "01:00";
					pauseE = "01:45";
				} else {
					tagN = `0${day.tagE}`.slice(-2);
					tag = `${tagN} | ${new Date(jahr, monat - 1, day.tagE).toLocaleDateString("de", {
						weekday: "short",
					})}`;
				}
				if (new Date(jahr, monat - 1, tag).getDay() == 5) {
					pauseA = "";
					pauseE = "";
				}

				let option = {};
				if (schicht == "N") {
					option.text = `${tag} | Nacht`;
				} else if (schicht == "BN") {
					option.text = `${tag} | Nacht / Bereitschaft`;
				} else {
					option.text = tag;
				}
				option.value = JSON.stringify({
					tagN,
					beginN: day.beginE,
					endeN: day.endeE,
					beginPauseN: pauseA,
					endePauseN: pauseE,
					nrN: "",
					dauerN: 0,
				});
				if (dataN) {
					dataN.forEach(value => {
						if (Number(value.tagN) == Number(tagN)) {
							option.disabled = true;
						}
					});
				}
				options.push(option);
			}
			return options;
		}
	}

	function customFooterButtton() {
		const btnManuell = document.createElement("button");
		btnManuell.className = "btn btn-info";
		btnManuell.type = "button";
		btnManuell.textContent = "Manuell";
		btnManuell.arialabel = "Manuell";
		btnManuell.dataset.bsDismiss = "modal";
		btnManuell.addEventListener("click", () => document.querySelector("#tableN").instance.options.editing.addRow());
		return btnManuell;
	}

	function SubmitEventListener() {
		return e => {
			if (form.checkValidity && !form.checkValidity()) return;
			e.preventDefault();

			addNebenTag(form);
		};
	}
}
function createEditorModalNeben(row, titel) {
	const { modal, form } = createModal(
		titel,
		true,
		null,
		createBodyElement,
		createEditorModalFooter(null, row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);

	return modal;

	function createBodyElement() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		let datum;
		const Monat = +localStorage.getItem("Monat");
		const Jahr = +localStorage.getItem("Jahr");
		if (row instanceof Row) {
			datum = dayjs([Jahr, Monat - 1, row.cells.tagN]);
		} else if (row instanceof CustomTable) {
			datum = dayjs([Jahr, Monat - 1, dayjs().date()]);
		} else throw Error("unbekannter Fehler");
		let column;

		column = row.columns.array.find(column => column.name === "tagN");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: datum.format("YYYY-MM-DD"),
				type: "date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Arbeitszeit"));

		["beginN", "endeN"].forEach(value => {
			column = row.columns.array.find(column => column.name === value);
			modalBody.appendChild(
				createModalBodyInputElement({
					divClass: "form-floating col-6 pb-3",
					title: column.title,
					name: column.name,
					value: (row.cells && row.cells[column.name]) || null,
					type: "time",
					required: true,
				})
			);
		});

		modalBody.appendChild(createModalBodyTitelElement("Pause"));

		["beginPauseN", "endePauseN"].forEach(value => {
			column = row.columns.array.find(column => column.name === value);
			modalBody.appendChild(
				createModalBodyInputElement({
					divClass: "form-floating col-6 pb-3",
					title: column.title,
					name: column.name,
					value: (row.cells && row.cells[column.name]) || null,
					type: "time",
				})
			);
		});

		modalBody.appendChild(createModalBodyTitelElement("Zulage"));

		column = row.columns.array.find(column => column.name === "dauerN");
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || 1,
				type: "number",
				required: true,
				min: 1,
				max: 1,
			})
		);

		column = row.columns.array.find(column => column.name === "nrN");
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: (row.cells && row.cells[column.name]) || null,
				required: true,
				options: [{ value: "040 Fahrentsch.", text: "040 Fahrentsch.", selected: true }],
			})
		);

		return modalBody;
	}

	function SubmitEventListener() {
		return e => {
			if (form.checkValidity && !form.checkValidity()) return;
			e.preventDefault();

			const row = modal.row,
				values = {
					tagN: dayjs(form.querySelector("#tagN").value).format("DD"),
					beginN: form.querySelector("#beginN").value,
					endeN: form.querySelector("#endeN").value,
					beginPauseN: form.querySelector("#beginPauseN").value,
					endePauseN: form.querySelector("#endePauseN").value,
					nrN: form.querySelector("#nrN").value,
					dauerN: Number(form.querySelector("#dauerN").value),
				};

			row instanceof Row ? row.val(values) : row.rows.add(values);

			Modal.getInstance(modal).hide();
			saveTableData("tableN", row.CustomTable || row);
		};
	}
}
function createShowModalNeben(row, title) {
	const { modal } = createModal(title, false, "sm", createModalBody, createShowModalFooter);
	return modal;

	function createModalBody() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";
		let column1, column2;

		column1 = row.columns.array.find(column => column.name === "tagN");
		modalBody.appendChild(
			createModalBodyShowElement({
				divClass: "mb-2 col-12 text-center",
				labelClass: "pe-3 align-middle col-form-label text-wrap fw-bold",
				spanClass: "align-middle my-auto",
				title: `${column1.title}:`,
				name: column1.name,
				text: column1.parser(row.cells[column1.name]),
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Arbeitszeit"));

		column1 = row.columns.array.find(column => column.name === "beginN");
		column2 = row.columns.array.find(column => column.name === "endeN");
		modalBody.appendChild(
			createModalBodyShowElementNeben({
				divClass: "mb-2 col-12 text-center",
				vorTitle: column1.parser(row.cells[column1.name]),
				title: "arrow_right_alt",
				nachTitle: column2.parser(row.cells[column2.name]),
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Pause"));

		column1 = row.columns.array.find(column => column.name === "beginPauseN");
		column2 = row.columns.array.find(column => column.name === "endePauseN");
		modalBody.appendChild(
			createModalBodyShowElementNeben({
				divClass: "mb-2 text-center",
				vorTitle: column1.parser(row.cells[column1.name]),
				title: "arrow_right_alt",
				nachTitle: column2.parser(row.cells[column2.name]),
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Zulage"));

		column1 = row.columns.array.find(column => column.name === "dauerN");
		column2 = row.columns.array.find(column => column.name === "nrN");
		modalBody.appendChild(
			createModalBodyShowElementNeben({
				divClass: "mb-2 col-12 text-center",
				vorTitleClass: "col-4 align-middle text-break my-auto",
				vorTitle: column1.parser(row.cells[column1.name]),
				titleClass: "align-middle fw-semibold mx-1",
				title: "X",
				nachTitleClass: "col-6 align-middle text-break my-auto",
				nachTitle: column2.parser(row.cells[column2.name]),
			})
		);

		return modalBody;

		function createModalBodyTitelElement(text) {
			const title = document.createElement("h4");
			title.className = "text-center mb-0";
			title.innerHTML = text;
			return title;
		}

		function createModalBodyShowElementNeben(options) {
			const showWrapper = document.createElement("div");
			showWrapper.className = options.divClass ?? "icon-ewt";

			showWrapper.appendChild(createModalBodySpanElement(options.vorTitleClass ?? "", options.vorTitle));
			showWrapper.appendChild(
				createModalBodySpanElement(options.titleClass ?? "material-icons-round big-icons", options.title)
			);
			showWrapper.appendChild(createModalBodySpanElement(options.nachTitleClass ?? "", options.nachTitle));

			return showWrapper;
		}
	}
}

export function DataN(data) {
	if (data == null) {
		if (localStorage.getItem("dataN") == null) return [];
		data = JSON.parse(localStorage.getItem("dataN"));
	}
	if (data.length === 0 || isObject(data[0])) return data;

	let data1 = [];
	data.forEach((row, i) => {
		data1[i] = {
			tagN: row[0],
			beginN: row[1],
			endeN: row[2],
			beginPauseN: row[3],
			endePauseN: row[4],
			nrN: row[5],
			dauerN: row[6] === "" ? 1 : Number(row[6]),
		};
	});
	return data1;
}

function addNebenTag(form) {
	let select = form.querySelector("#tagN");
	let idN = select.selectedIndex;
	if (idN < 0) return;
	let daten = JSON.parse(select.value);
	daten.nrN = form.querySelector("#Nebenbezug").value;
	daten.dauerN = +form.querySelector("#AnzahlN").value;
	console.log(daten);
	select[idN].selected = false;
	select[idN].disabled = true;
	idN++;
	while (idN < select.length) {
		if (!select[idN].disabled) {
			select[idN].selected = true;
			break;
		}
		idN++;
	}

	const ftN = document.querySelector("#tableN").instance;
	ftN.rows.add(daten);
	saveTableData("tableN", document.querySelector("#tableN").CustomTable);
}
