import { saveDaten } from "./Core.js";
import { createSnackBar } from "./js-snackbar.js";

window.addEventListener("load", () => {
	document.querySelector("#formEinstellungen").addEventListener("submit", e => {
		e.preventDefault();
		saveDaten(document.getElementById("btnSaveEinstellungen"));
	});

	if (localStorage.getItem("VorgabenU"))
		generateEingabeMaskeEinstellungen(JSON.parse(localStorage.getItem("VorgabenU")));
});

export function generateEingabeMaskeEinstellungen(VorgabenU) {
	Object.keys(VorgabenU.pers).forEach(key => {
		if (key == "Name") return;
		document.getElementById(key).value = VorgabenU.pers[key];
	});

	Object.keys(VorgabenU.aZ).forEach(key => {
		document.getElementById(key).value = VorgabenU.aZ[key];
	});

	let tbody = document.getElementById("TbodyTätigkeitsstätten");
	tbody.innerHTML = "";

	let td, input;
	VorgabenU.fZ.forEach(row => {
		let tr = tbody.insertRow();
		createInput(tr, 0, "Text", row.key);
		createInput(tr, 1, "Text", row.text);
		createInput(tr, 2, "time", row.value);
	});

	for (let i = 0; i < 5; i++) {
		let tr = tbody.insertRow();
		createInput(tr, 0, "Text", "");
		createInput(tr, 1, "Text", "");
		createInput(tr, 2, "time", "");
	}

	function createInput(tr, position, type, value) {
		td = tr.insertCell(position);
		input = document.createElement("input");
		input.type = type;
		input.className = "form-control text-center";
		input.value = value;
		td.appendChild(input);
	}
}

export function saveEinstellungen() {
	let VorgabenU = JSON.parse(localStorage.getItem("VorgabenU"));

	Object.keys(VorgabenU.pers).forEach(key => {
		VorgabenU.pers[key] = document.getElementById(key).value;
	});

	Object.keys(VorgabenU.aZ).forEach(key => {
		VorgabenU.aZ[key] = document.getElementById(key).value;
	});

	VorgabenU.fZ = table_to_array_einstellungen("TbodyTätigkeitsstätten");
	if (VorgabenU.pers.TB == "Tarifkraft") {
		document.getElementById("Neben-tab").parentElement.classList.remove("d-none");
	} else {
		document.getElementById("Neben-tab").parentElement.classList.add("d-none");
	}

	localStorage.setItem("VorgabenU", JSON.stringify(VorgabenU));

	function table_to_array_einstellungen(table_id) {
		const myData = document.getElementById(table_id).rows;
		let my_liste = [];
		for (const myDatum of myData) {
			let el = myDatum.children;
			const key = el[0].children[0].value;
			if (!key) {
				continue;
			}
			const text = el[1].children[0].value;
			const value = el[2].children[0].value;
			if (!text || !value) {
				createSnackBar({
					message: `Einstellungen > Fahrzeiten > "${key}": Beschreibung / Fahrzeit fehlt`,
					status: "error",
					timeout: 3000,
					position: "br",
					fixed: true,
				});
				throw Error("Beschreibung / Fahrzeit fehlt");
			}
			my_liste.push({ key, text, value });
		}

		return my_liste;
	}
}
